import { LanguageEncoder } from '../language-encoder'
import { StringTokenizer } from '../string-tokenizer'
import { Token } from '../token'
import { TokenArray } from '../token-array'

import { C64Chr } from './c64-chr'
import { V2 } from './v2-consts'
import { V2Tokenizer } from './v2-tokenizer'

export class V2Encoder extends LanguageEncoder {
    // static readonly RX_LEXER: RegExp = /(?<CMD>end|for|next|data|input#|input|dim|read|let|goto|run|if|restore|gosub|return|rem|stop|on|wait|load|save|verify|def|poke|print#|print|cont|list|clr|cmd|sys|open|close|get|new)|(?<FUNC>tab\(|to|fn|spc\(|then|not|step|\+|-|\*|\/|\^|and|or|>|=|<|sgn|int|abs|usr|fre|pos|sqr|rnd|log|exp|cos|sin|tan|atn|peek|len|str\$|val|asc|chr\$|left\$|right\$|mid\$)|(?<STR>\"[^\"]*\")|(?<NUM>(\d+[.]?\d*)|(\d+)|([.]\d*))|(?<VAR>[a-z][a-z0-9]*[$%(]?)|(?<OTHER>.)/g

    static encoders: Record<string, any> = {
        idType: V2Encoder.stringEncoder,
        STR: V2Encoder.stringEncoder,
        NUM: V2Encoder.stringEncoder,
        VAR: V2Encoder.stringEncoder,
        SEP: V2Encoder.stringEncoder,
        CMD: V2Encoder.cmdEncoder,
        FUNC: V2Encoder.cmdEncoder,
        operator: V2Encoder.cmdEncoder,
        OTHER: V2Encoder.stringEncoder,
    }

    static stringEncoder(token: Token): number[] {
        if (token.key == 'other' && token.value == '?') return [V2.TTC_COMMAND_TOKENS['print']]

        const props = {
            tokens: V2.TTC_STRING_TOKENS,
            normalConverter: C64Chr.textToPetscii,
        }
        const { byteArray, status, _invalidCodes } = StringTokenizer.convertStringToBytes(
            token.repr,
            props,
        )
        /* const bytes = token.repr.split('').map( (ch: string) => {
            return textToPetscii(ch)
        }) */

        if (status !== 'OK') console.log(status)

        return byteArray
    }

    static cmdEncoder(token: Token) {
        return [V2.TTC_COMMAND_TOKENS[token.repr]]
    }

    static encodeTextAsBytes(text: string): number[] {
        const tokenizer = new V2Tokenizer()
        tokenizer.tokenizeString(text)
        return V2Encoder.encodeTokens(tokenizer.tokens)
    }

    static encodeTokens(array: TokenArray): number[] {
        let address = 0x801
        const lines: any[] = []

        const lineData = { nextAddr: 0, line: null as unknown as number, data: [] as number[] }

        function closeLast() {
            if (lineData.data.length > 0) {
                address += lineData.data.length + 5
                lineData.nextAddr = address

                const lineDataCopy = Object.assign({}, lineData)
                lines.push(lineDataCopy)

                lineData.data = []
                lineData.nextAddr = 0
            }
        }

        array.forEach((token: Token) => {
            if (token.category == 'LABEL') {
                closeLast()

                const lineVal = parseInt(token.value, 10)
                lineData.line = lineVal

                return
            }

            const encoderFunction =
                V2Encoder.encoders[token.key] || V2Encoder.encoders[token.category]
            if (encoderFunction) {
                const data = encoderFunction(token)
                lineData.data.push(...data)
            }
        })
        closeLast()

        const finalBytes: number[] = []

        lines.forEach((line) => {
            finalBytes.push(line.nextAddr & 0xff, line.nextAddr >> 8)
            finalBytes.push(line.line & 0xff, line.line >> 8)
            finalBytes.push(...line.data, 0)
        })

        finalBytes.push(0, 0)

        return finalBytes
    }

    /*
    deployCode(data: number[], state) {
        const start = 0x801
        const memData = { start, data }

        app.emulator.setDataInRam(memData)
        app.emulator.onLoaded(memData);

        const memEnd = start + data.length + 5;

        [0x2e, 0x30, 0x32].forEach(changeAddr => {
            state.globalState.emulator.bus.write(changeAddr, (memEnd >> 8))
            state.globalState.emulator.bus.write(changeAddr - 1, memEnd & 0xff)
        })
    }
    */
}
