/** A single Token */
export class Token {
    typeMapper: Record<string, string[]>

    static defaultMapper = {
        remark: ['remark', 'rem_multi', 'docstr'],
        number: ['hexNum', 'floatNum', 'intNum'],
        string: ['strDQuote', 'strSQuote', 'strBQuote'],
        binaryOp: [
            '+',
            '-',
            '<',
            '<=',
            '==',
            '>',
            '>=',
            '!=',
            '!==',
            '===',
            '||',
            '&&',
            '*',
            '/',
            '%',
            '|',
            '&',
        ],
        unaryOp: ['(', '-', '~'],
        assignment: ['=', '|=', '&=', '+=', '-=', '*=', '/=', '^='],
    }

    constructor(
        public key: string,
        public value: string,
        public pos: number,
        public repr: string,
        public reprPretty: string,
        public category: string,
        public spanClass: string,
        typeMapper: Record<string, string[]> | null = null,
    ) {
        this.typeMapper = typeMapper || Token.defaultMapper
    }

    /** Is the token a remark? */
    isRemark() {
        return this.typeMapper.remark.indexOf(this.key) >= 0
    }

    /** Is the token a number? */
    isNumber() {
        return this.typeMapper.number.indexOf(this.key) >= 0
    }

    /** Is the token a string? */
    isString() {
        return this.typeMapper.string.indexOf(this.key) >= 0
    }

    /** Is the token a unary operator? */
    isUnaryOperation() {
        return this.typeMapper.unaryOp.indexOf(this.value) >= 0
    }

    /** Is the token a binary operator? */
    isBinaryOperation() {
        return this.typeMapper.binaryOp.indexOf(this.value) >= 0
    }

    /** Is the token an assignment operator? */
    isAssignment() {
        return this.typeMapper.assignment.indexOf(this.value) >= 0
    }

    /** Is the token of a given type? */
    isType(typeName: string) {
        return (
            this.typeMapper[typeName].indexOf(this.key) >= 0 ||
            this.typeMapper[typeName].indexOf(this.value) >= 0
        )
    }
}
