import { TokenBuilderItem, Tokenizer, TokenizerDefinition } from '../tokenizer'
import { TokenizerSettings } from '../tokenizer-settings'

import { V2Decoder } from './v2-decoder'
import { V2Prettifier } from './v2-prettifier'

export class V2Tokenizer extends Tokenizer {
    constructor(_dummySettings: TokenizerSettings = <TokenizerSettings>{}) {
        const special: TokenizerDefinition = {
            v2Identifier: [
                8000,
                String.raw`  (?<identifier>[\w]{1,2})      // First max 2 chars
                                            (?<idExtra>[\w]*)             // Remaining are not used
                                            (?<idType>[$%]?)              // Type declaration: $ or %
                                            `,
            ],

            v2Label: [2000, String.raw`^(?<whiteSpc3>\s*)(?<label>[\d]+)`],
            v2Command: [
                6000,
                String.raw`(?<command>end|for|next|data|input#|input|dim|read|let|goto|run|if|restore|gosub|return|rem|stop|on|wait|load|save|verify|def|poke|print#|print|cont|list|clr|cmd|sys|open|close|get|new)`,
            ],
            v2Function: [
                6100,
                String.raw`(?<identifier2>[\w]{0,2})?(?<func>tab\(|to|fn|spc\(|then|not|step|and|or|sgn|int|abs|usr|fre|pos|sqr|rnd|log|exp|cos|sin|tan|atn|peek|len|str\$|val|asc|chr\$|left\$|right\$|mid\$)`,
            ],
            V2Operator: [6200, String.raw`(?<operator>[+\-*/^>=<])`],
            v2Sep: [7000, String.raw`(?<commandSep>[:])`],
            v2Rem: [1000, String.raw`(?<commandRem>rem)(?<rem>.*$)`],
            newLines: [3050, String.raw`(?<newLines>(\r\n|\n|\r)+)`],
            whiteSpace: [3100, String.raw`(?<whiteSpc>[\t \u00a0]+)`],
        }

        const rx = Tokenizer.buildRxString(
            [
                TokenBuilderItem.numInt,
                TokenBuilderItem.numFloat,
                TokenBuilderItem.strDQuote,
                'v2Identifier',
                'v2Label',
                'V2Operator',
                'v2Command',
                'v2Function',
                'newLines',
                'v2Sep',
                'v2Rem',
                'whiteSpace',
            ],
            special,
        )

        const mapper = {
            // 'SYS': 'color:#d8e;font-weight:600',
            // 'ERR': 'color:red; text-decoration-line: underline; text-decoration-style: wavy; text-decoration-color: red',
            // 'SPEC': 'color:#ba8;font-style:italic;font-weight:500',

            command: 'CMD',
            commandRem: 'CMD',
            func: 'FUNC',
            strDQuote: 'STR',
            label: 'LABEL',
            floatNum: 'NUM',
            intNum: 'NUM',

            identifier: 'VAR',
            identifier2: 'VAR',
            idExtra: 'EXTRA',
            idType: 'FUNC',

            commandSep: 'SEP',
            whiteSpc: 'SPC',
            rem: 'REM',
            other: 'OTHER',
        }

        const settings = new TokenizerSettings({
            rxPattern: rx,
            mapper,
            htmlClassPrefix: 'editor-style-',
            PrettifierClass: V2Prettifier,
        })

        super(settings)
    }

    getTokensFromMemory(memory: number[]) {
        const rawTokens = V2Decoder.getTokensFromMemory(memory)
        this.reset()

        rawTokens.forEach((token) => {
            this.addToken(token.key, String(token.value), token.pos)
        })

        // console.log(this.tokenizer.tokens)
    }

    tokenizeStr(text: string) {
        this.tokenizeString(text)
    }
}
