import {
    TOKENS_BY_NAME,
    DATA_TYPES,
    DATA_TYPES_BY_SHORT_CODE,
    DataTypeDetails,
    scrTokensTTC,
    GVAR_SHORT_TO_CODE_DICT,
} from '@zoli8000/gb-consts'

import { StringTokenizer } from '../string-tokenizer'
import { Token } from '../token'
import { Tokenizer } from '../tokenizer'

import { DEFAULT_STRING_TOKENIZER_PROPS } from './gb-encoder'
import { NumberTokenizer, defaultNumberEncoder, GBTypeCodes } from './gb-number-encoder'

export type SizeDetails = {
    dataLength: number
    memBytes: number
}

export type DataTypeDetailsWithSize = DataTypeDetails & SizeDetails

export const DEFAULT_STRING_LENGTH = 254

/** Custom error for invalid variable names */
export class InvalidVariableNameError extends Error {
    constructor(varName: string, errorSubtype: string) {
        const errorMessage = `Invalid variable name: ${varName}. ${errorSubtype}`

        super(errorMessage)
        this.name = 'InvalidVariableNameError'

        Object.setPrototypeOf(this, new.target.prototype)
    }
}

function getSVarList(vars: string[]): string {
    const sorted = vars.sort((a, b) => GVAR_SHORT_TO_CODE_DICT[a] - GVAR_SHORT_TO_CODE_DICT[b])

    // Step 2: Group consecutive items using reduce
    const groups = sorted.reduce<string[][]>((acc, curr) => {
        if (acc.length === 0) return [[curr]]

        const lastGroup = acc[acc.length - 1]
        const prev = lastGroup[lastGroup.length - 1]

        if (GVAR_SHORT_TO_CODE_DICT[curr] === GVAR_SHORT_TO_CODE_DICT[prev] + 1) {
            lastGroup.push(curr)
        } else {
            acc.push([curr])
        }

        return acc
    }, [])

    return groups
        .map((group) =>
            group.length > 2 ? `${group[0]}-${group[group.length - 1]}` : group.join(''),
        )
        .join('')
}

export class DimTokenizer {
    numberTokenizer: NumberTokenizer
    variableMapping: Record<string, any>

    constructor() {
        this.numberTokenizer = new NumberTokenizer(GBTypeCodes, defaultNumberEncoder)
        this.variableMapping = {}
    }

    getDimText(tokens: Token[]): string {
        let dimText = ''
        tokens.forEach((token) => {
            if (token.category === 'dimDetailsLong') {
                dimText += token.value + '\n'
            }
        })
        return dimText
    }

    getType(typeStr: string): DataTypeDetailsWithSize | null {
        const sizeDetails: SizeDetails = { dataLength: 1, memBytes: 1 }

        const rxSizeSplit = /^(?<baseType>\$|FIXSTR|.*)[.]?(?<dataLength>\d+)?$/
        const match = typeStr.match(rxSizeSplit)
        let dataLength = 1

        if (!match?.groups) {
            throw new Error(`Invalid type string format: ${typeStr}`)
        }

        if (match.groups.baseType === '$' || match.groups.baseType === 'FIXSTR') {
            if (match?.groups.dataLength) {
                dataLength = Number(match.groups.dataLength)
            } else {
                dataLength = DEFAULT_STRING_LENGTH
            }

            sizeDetails.dataLength = dataLength
            sizeDetails.memBytes = dataLength + 2
        }

        const baseType = match.groups.baseType

        if (baseType in DATA_TYPES_BY_SHORT_CODE) {
            return { ...DATA_TYPES_BY_SHORT_CODE[baseType], ...sizeDetails }
        }

        if (baseType.toUpperCase() in DATA_TYPES) {
            return { ...DATA_TYPES[baseType.toUpperCase()], ...sizeDetails }
        }

        throw new Error(`Invalid type string format: ${typeStr}`)
    }

    rxTypesLong = Object.keys(DATA_TYPES).join('|')
    rxTypesShort = Object.keys(DATA_TYPES_BY_SHORT_CODE).join('').replace(/0/, '') // bWwfSL%cd$BFsl

    parseDimShort(_dimText: string) {
        throw Error('Not implemented dim short parse!')
    }

    /** Parse dim string, create variable mapping */
    parseDimLong(dimText: string, isShortVarKept: boolean) {
        const rx = Tokenizer.verboseRegexp(
            String.raw`
            (?<wspc3>^\s*)(?<type>
                ${this.rxTypesLong} |
                ([$]\d+) |
                [${this.rxTypesShort}]
            )
            (?<arr_sign>(<[=-]{1,2}))?
            (?<arr_size>\d*)
            (?=$|\s+) |
            (
                (?<var_name>
                    [a-zA-Z][a-zA-Z_×]*
                )
                (?<wspc1>\s*[,;]?\s*)
                (
                    (?<comment>[-|\/]{2}.*?$) |
                    (?<break>,|$)
                )
            ) |
            (?<wspc2>[\t\x20]+\n?|\n) |

            (?<error_word>\w+) |
            (?<error>.)`,
            'gmi',
        )

        // TODO: move this to utility function
        rx.lastIndex = 0
        let match: RegExpExecArray | null
        let textUsedLength = 0
        let lastValue = 0

        const errors: any[] = []
        const vars: Record<string, any> = {}

        let currentType = null as DataTypeDetailsWithSize | null
        let currentArrLength = 0 as number | null
        let currentVarName = null as string | null
        let idx = 0

        while ((match = rx.exec(dimText))) {
            if (!match || !match.groups || !match[0].length || match.index == undefined) {
                throw Error('Update regexp - empty result!')
            }

            Object.entries(match.groups!).forEach(([subKey, subValue]) => {
                if (!subValue) return

                textUsedLength += subValue.length

                if (subKey === 'type') {
                    currentType = this.getType(subValue)
                    currentArrLength = 0
                }

                if (subKey === 'arr_size') {
                    currentArrLength = parseInt(subValue, 10)
                }
                if (subKey === 'var_name') {
                    // Allow single-char or char+× if short vars, else require at least 2 chars, no × allowed
                    this.assertVarNameValid(subValue, isShortVarKept, true)

                    currentVarName = subValue
                    const currentVarNameNormalized = currentVarName.replace(/×/g, 'x')

                    const idxScore = GVAR_SHORT_TO_CODE_DICT[currentVarNameNormalized] || idx + 100
                    vars[currentVarName] = {
                        type: currentType,
                        arr_size: currentArrLength,
                        comment: '',
                        idx: idx,
                        idxScore,
                    }
                    idx++
                }

                if (subKey === 'comment') {
                    if (currentVarName) {
                        vars[currentVarName].comment = subValue.replace(/^--\s*/g, '') || ''
                    }
                }

                if (subKey === 'error') {
                    errors.push([lastValue, match?.index, subValue])
                }

                if (subKey === 'error_word') {
                    errors.push([lastValue, match?.index, `Invalid word: ${subValue}`])
                }
                // console.log(`Found: ${subKey} = ${subValue} at index ${match?.index}`)
            })

            if (match.index != lastValue) {
                errors.push([lastValue, match.index, match[0]])
            }

            lastValue = match.index + match[0].length

            if (textUsedLength != lastValue) {
                errors.push([lastValue, match?.index, dimText.substring(lastValue, match?.index)])
            }
        }

        if (lastValue != dimText.length) {
            errors.push([lastValue, 0])
        }

        if (textUsedLength != dimText.length) {
            throw Error('Update regexp - characters are missing!')
        }

        this.variableMapping = vars
        return { vars, errors }
    }

    /** Get variable mapping */
    getVariableMapping(): Record<string, any> {
        return this.variableMapping
    }

    private assertVarNameValid(varName: string, isShortVarKept: boolean, isVerbose: boolean): void {
        const isShortVar = /^[a-zA-Z]×?$/.test(varName)
        const isLongVar = /^[a-zA-Z][a-zA-Z_]{1,}$/.test(varName)

        if (!isVerbose && !isShortVar) {
            throw new InvalidVariableNameError(
                varName,
                'Only a single letter or a letter with × is allowed.',
            )
        } else if (varName in TOKENS_BY_NAME) {
            throw new InvalidVariableNameError(varName, 'Variable name cannot be a keyword.')
        } else if (isVerbose && !isShortVarKept && isShortVar) {
            throw new InvalidVariableNameError(
                varName,
                'No single letter variable names are allowed. Use #directshort directive to allow them.',
            )
        } else if (
            (isVerbose && isLongVar) ||
            (isVerbose && isShortVarKept && isShortVar) ||
            (!isVerbose && isShortVar)
        ) {
            return
        }

        throw new InvalidVariableNameError(
            varName,
            'Invalid variable name, ony a-z,A-Z, A×-Z×, _ are allowed.',
        )
    }
    /**
     * Assigns/translates variable names and returns a mapping:
     * { transformedVarName: { ...varData, transformedVarName } }
     */
    assignVariableNames(dimData: Record<string, any>): Record<string, any> {
        const sortedDimData = Object.entries(dimData).sort((a, b) => a[1].idxScore - b[1].idxScore)
        const possibleVars = [...Object.keys(GVAR_SHORT_TO_CODE_DICT)]
        const mappedVars: Record<string, any> = {}

        sortedDimData.forEach(([varName, varDetails]) => {
            const isShortVar = varDetails.idxScore < 100
            let transformedVarName = varName

            if (isShortVar) {
                const shortVarIndex = possibleVars.indexOf(varName)
                if (shortVarIndex !== -1) {
                    possibleVars.splice(shortVarIndex, 1)
                } else {
                    throw new InvalidVariableNameError(
                        varName,
                        'Short variable name not found in possible variables.',
                    )
                }
            } else {
                transformedVarName = possibleVars.shift() || ''
                if (!transformedVarName) {
                    throw new InvalidVariableNameError(
                        varName,
                        'No more short variable names available.',
                    )
                }
            }

            mappedVars[transformedVarName] = {
                ...varDetails,
                varName,
                transformedVarName,
                isShortVar,
            }
        })

        return mappedVars
    }

    /**
     * Encodes the variable mapping (from assignVariableNames) to bytes.
     */
    encodeDimLong(mappedVars: Record<string, any>): number[] {
        const encoded: number[] = []
        const skipType = null

        const varsByType: Record<string, any[]> = {}

        Object.values(mappedVars).forEach((varData: any) => {
            const { type, arr_size } = varData
            const { encodedType, encodedTypeBytes } = this.getTypeCode(type, arr_size)
            varData.typeCode = encodedType
            varData.encodedTypeBytes = encodedTypeBytes
            const typeCode = encodedType
            varsByType[typeCode] = varsByType[typeCode] || []
            varsByType[typeCode].push(varData)
        })

        const orderedTypes = Object.values(mappedVars)
            .sort((a: any, b: any) => a.idx - b.idx)
            .map((data: any) => data.typeCode)
            .filter((type, index, self) => self.indexOf(type) === index)

        const encodedBytesByType: Record<string, number[]> = {}

        Object.entries(varsByType).forEach(([typeCode, vars]) => {
            if (typeCode === skipType) return

            const encodedData: number[] = []

            if (vars[0].type == skipType) return

            encodedData.push(...vars[0].encodedTypeBytes)
            encodedBytesByType[typeCode] = encodedData

            const varNames = vars.map((varData: any) => varData.transformedVarName)
            const varNamesShortened = getSVarList(varNames)

            const byteData = StringTokenizer.convertStringToBytes(
                varNamesShortened,
                DEFAULT_STRING_TOKENIZER_PROPS,
            ).byteArray
            encodedData.push(...byteData)
        })

        orderedTypes.forEach((typeCode) => {
            const encodedValues = encodedBytesByType[typeCode]
            encoded.push(...encodedValues, scrTokensTTC[','])
        })

        encoded.pop()
        return encoded
    }

    defaultSizePostfix = {
        128: '$',
        254: '',
    }

    /** Type code.
     * A single byte for normal types with short code.
     * For strings, it can contain a length index.
     * Also for arrays it can contain a length index.
     * For example, $10 is a string of length 10, $ is a string of default length.
     * Arrays:
     * - w10 is an array of 10 words
     * - $20 is a string of length 20
     * - $20<-3 is an array of 3 strings of length 20
     */
    getTypeCode(
        typeDetails: DataTypeDetailsWithSize,
        arrSize: number,
    ): { encodedType: string; encodedTypeBytes: number[] } {
        let encodedType = ''
        const encodedTypeBytes: number[] = []

        if (typeDetails.shortCode != '$') {
            encodedType += typeDetails.shortCode
            encodedTypeBytes.push(scrTokensTTC[typeDetails.shortCode as keyof typeof scrTokensTTC])
        } else {
            if (
                Object.keys(this.defaultSizePostfix).includes(typeDetails.dataLength.toString(10))
            ) {
                const postfix =
                    this.defaultSizePostfix[
                        typeDetails.dataLength as keyof typeof this.defaultSizePostfix
                    ]
                encodedType += `$${postfix}`
                encodedTypeBytes.push(
                    scrTokensTTC['$'],
                    ...StringTokenizer.convertStringToBytes(postfix, DEFAULT_STRING_TOKENIZER_PROPS)
                        .byteArray,
                )
            } else {
                encodedType += `$${typeDetails.dataLength}`
                encodedTypeBytes.push(
                    scrTokensTTC['$'],
                    ...this.numberTokenizer.integerToArray(typeDetails.dataLength.toString(10)),
                )
            }
        }

        if (arrSize > 0) {
            encodedType += `{'<=='}${arrSize.toString()}}`
            if (typeDetails.shortCode == '$') {
                encodedTypeBytes.push(scrTokensTTC['<=='])
            }
            encodedTypeBytes.push(...this.numberTokenizer.integerToArray(arrSize.toString(10)))
        }
        return { encodedType, encodedTypeBytes }
    }
}
