import { LanguageDecoder, DECODER_SCOPE } from '../language-decoder'
import { TokenArray } from '../token-array'

import { C64Chr } from './c64-chr'
import { GB } from './gb-consts'

export class GBDecoder implements LanguageDecoder {
    static STR_CODES = {
        print: GB.CTT_STRING_TOKENS,
        sprite: GB.CTT_SPRITE_TOKENS,
        font: GB.CTT_STRING_TOKENS, // TODO!!!!
    }
    static getTokensFromMemory(data: number[], start = 0x801): TokenArray {
        const getWord = function (lo: number, hi: number): number {
            return lo + hi * 0x100
        }

        const tokens: TokenArray = []

        let idx = 0
        let nextAddr = start
        let lastCommand = ''

        while (idx < data.length) {
            if (nextAddr < idx + start || nextAddr < start || nextAddr > start + data.length) {
                console.log(`Invalid address ${nextAddr}`)
                break
            }

            idx = nextAddr - start

            nextAddr = getWord(data[idx], data[idx + 1])
            if (nextAddr == 0) break

            const line = getWord(data[idx + 2], data[idx + 3])
            tokens.push({ key: 'label', value: line.toString(10), pos: idx + start })

            idx += 4

            let scope: DECODER_SCOPE
            scope = DECODER_SCOPE.scopeCommand

            const item = { key: 'error', value: 'error', escapedMode: '', escapedValue: '' }
            let byte: number = 0

            function setCommandFunction(item: Record<string, string>) {
                if (byte >= 128) {
                    const text = GB.CTT_COMMAND_TOKENS[byte]

                    if (!text) return

                    item.key = byte > 162 ? 'func' : 'command'
                    item.value = text
                    lastCommand = text
                }

                if (byte >= 65 && byte <= 90) {
                    item.key = 'identifier'
                    item.value = item.escapedValue
                }

                if (byte == 32) {
                    item.key = 'whiteSpc'
                    item.value = item.escapedValue
                }
            }

            while (idx + start < nextAddr) {
                byte = data[idx]

                if (byte == 0) {
                    break
                }

                item.value = 'error'
                item.key = 'error'
                ;[item.escapedMode, item.escapedValue] = C64Chr.petsciiCodeToEscaped(byte)

                switch (scope) {
                    case DECODER_SCOPE.scopeCommand:
                        setCommandFunction(item)

                        scope = DECODER_SCOPE.scopeArgs
                        if (item.value == 'rem') scope = DECODER_SCOPE.scopeRem
                        break

                    case DECODER_SCOPE.scopeRem:
                        item.key = 'rem'
                        item.value = item.escapedValue
                        break

                    case DECODER_SCOPE.scopeString:
                        if (item.escapedValue == '"') {
                            scope = DECODER_SCOPE.scopeArgs
                            item.key = 'other'
                        } else {
                            item.key = 'strDQuote'
                        }

                        if (item.escapedMode == 'code') {
                            const CTT =
                                GBDecoder.STR_CODES[
                                    lastCommand as keyof typeof GBDecoder.STR_CODES
                                ] || GBDecoder.STR_CODES.print
                            item.value = CTT[byte] ? `{${CTT[byte]}}` : `{${item.escapedValue}}`
                        } else {
                            item.value = item.escapedValue
                        }
                        break
                    case DECODER_SCOPE.scopeArgs:
                        item.value = item.escapedValue
                        item.key = 'other'

                        setCommandFunction(item)

                        if (item.value == '"') {
                            scope = DECODER_SCOPE.scopeString
                        } else if (item.value == ':') {
                            scope = DECODER_SCOPE.scopeCommand
                        } else if ((item.value >= '0' && item.value <= '9') || item.value == '.') {
                            item.key = 'floatNum'
                        }
                        break
                }

                tokens.push({ key: item.key, value: item.value, pos: idx + start })
                idx++
            }
            tokens.push({ key: 'newLines', value: '\n', pos: idx + start })
        }

        return tokens
    }

    static tokenArrayToRepr(tokens: TokenArray): string {
        let text = ''
        tokens.forEach((token) => {
            text += token.value
        })
        return text
    }
}
