export const enum SpecialBoolType {
    unset,
    mutableTrue,
    mutableFalse,
    finalTrue,
    finalFalse,
}

export class SpecialBool {
    _value: SpecialBoolType = SpecialBoolType.unset

    constructor(initValue: SpecialBoolType = SpecialBoolType.unset) {
        this._value = initValue
    }

    isTrue() {
        return (
            this._value === SpecialBoolType.mutableTrue || this._value === SpecialBoolType.finalTrue
        )
    }

    isFalse() {
        return (
            this._value === SpecialBoolType.mutableFalse ||
            this._value === SpecialBoolType.finalFalse
        )
    }

    isFinal() {
        return (
            this._value === SpecialBoolType.finalTrue || this._value === SpecialBoolType.finalFalse
        )
    }

    set value(value: SpecialBoolType | boolean) {
        if (this.isFinal()) {
            return
        }

        if (value === true) {
            this._value = SpecialBoolType.mutableTrue
        } else if (value === false) {
            this._value = SpecialBoolType.mutableFalse
        } else {
            this._value = value
        }
    }
}
