/**
 * Settings for the parser
 *
 * posToStringFn: function converting a character index position to human readable form (e.g. Tokenizer.posToString)
 *
 */
export class ParserSettings {
    posToStringFn: (pos: number) => string
    keys: Record<string, string>

    static defaultKeys = {
        _whsp: 'whiteSpc',
        _docstr: 'docstr',
    }

    constructor(settings: Partial<ParserSettings>) {
        this.posToStringFn = settings.posToStringFn || String
        this.keys = settings.keys || ParserSettings.defaultKeys
    }
}
