/**
- Preprocessor #1: read directives (strict, verbose) -> directives, consts -> consts, dim -> dim, debug -> debug
- Preprocessor #2: detect short varnames for verbose, create mapping, count def, fast and variable refs
- Preproc: restructure if allowed (move single defs inline -> #inline)
*/

import { LanguagePreprocessor } from '../language-preprocessor'
import { SpecialBool, SpecialBoolType } from '../special-bool'
import { TokenArray } from '../token-array'

import { GBTokenizerSettings, VerboseMode } from './gb-tokenizer'

export type GbDirectiveKey =
    | 'verbose'
    | 'compact'
    | 'strict'
    | 'loose'
    | 'inline'
    | 'safe'
    | 'trusted'
    | 'directshort'
export type GbDirectives = Record<GbDirectiveKey, any>

export class GBLanguagePreprocessor extends LanguagePreprocessor {
    /** Read potential directives from first line.
     *
     * Possible directives: \
     *      #verbose        use verbose variable and function names \
     *      #compact        use single char variable and function names (e.g. a, A, Ax) \
     *      #strict         allow only variable names initiated with DIM \
     *      #loose          no need to init variable names, not as safe
     *      #inline         add defs that are used only once inline  \
     *      #safe           do not execute js code \
     *      #trusted        execute js code after user confirmation \
     *      #directshort    allow short variable names (e.g. a, A, Ax) in compact mode \
     *
     */

    directives: GbDirectives = {
        verbose: new SpecialBool(SpecialBoolType.mutableTrue),
        compact: new SpecialBool(SpecialBoolType.mutableFalse),
        strict: new SpecialBool(SpecialBoolType.mutableTrue),
        loose: new SpecialBool(SpecialBoolType.mutableFalse),
        inline: new SpecialBool(SpecialBoolType.mutableFalse),
        safe: new SpecialBool(SpecialBoolType.mutableTrue),
        trusted: new SpecialBool(SpecialBoolType.mutableFalse),
        directshort: new SpecialBool(SpecialBoolType.mutableFalse),
    }

    directiveFunctions: Record<GbDirectiveKey, () => void> = {
        verbose: () => {
            this.directives.verbose.value = true
            this.directives.compact.value = false
        },
        compact: () => {
            this.directives.verbose.value = false
            this.directives.compact.value = true
        },
        strict: () => {
            this.directives.strict.value = true
            this.directives.loose.value = false
        },
        loose: () => {
            this.directives.strict.value = false
            this.directives.loose.value = true
        },
        inline: () => {
            this.directives.inline.value = true
        },
        safe: () => {
            this.directives.safe.value = true
            this.directives.trusted.value = false
        },
        trusted: () => {
            this.directives.safe.value = false
            this.directives.trusted.value = true
        },
        directshort: () => {
            this.directives.directshort.value = true
        },
    }

    setSettings(gbSettings: Partial<GBTokenizerSettings>) {
        if (gbSettings.verboseMode === VerboseMode.VERBOSE) {
            this.directives.verbose.value = SpecialBoolType.finalTrue
            this.directives.compact.value = SpecialBoolType.finalFalse
        } else if (gbSettings.verboseMode === VerboseMode.SHORT) {
            this.directives.verbose.value = SpecialBoolType.finalFalse
            this.directives.compact.value = SpecialBoolType.finalTrue
        } else if (gbSettings.verboseMode === VerboseMode.AUTO) {
            // TODO - run preprocessor to detect the mode
            this.directives.verbose.value = true
            this.directives.compact.value = false
        }
    }

    analyzeVarsFromTokenized(tokens: TokenArray) {
        const varCount: Record<string, number> = {}

        tokens.forEach((token) => {
            if (token.category === 'VAR') {
                varCount[token.value] = (varCount[token.value] || 0) + 1
            }
        })

        return varCount
    }

    getDimDetails(tokens: TokenArray) {
        let memSettings = 0
        const dimDetails: Record<string, number> = {}
        let _currentType = 0
        let _arrayLength = 0

        let dimIndex = tokens.findIndex((token) => token.category === 'DIM')
        if (dimIndex === -1) {
            return { memSettings }
        }

        if (tokens[dimIndex + 1]?.category == 'intNum') {
            memSettings = tokens[dimIndex + 1].value
            dimIndex += 2
        }

        // Type (array length) variables separated by commas, if ';' found, finish loop
        while (dimIndex < tokens.length) {
            if (tokens[dimIndex].category === 'commandSep' && tokens[dimIndex].value === ';') {
                break
            }

            if (tokens[dimIndex].category === 'idType') {
                _currentType = tokens[dimIndex].value

                if (tokens[dimIndex + 1]?.category === 'intNum') {
                    _arrayLength = tokens[dimIndex + 1].value
                    dimIndex++
                }
                dimIndex++

                while (tokens[dimIndex]?.category === 'VAR') {
                    const varName = tokens[dimIndex].value
                    dimDetails[varName] = tokens[dimIndex].value
                }
                dimIndex++
            }

            dimIndex++
        }
    }
}
