/**
    A single instance should be used.
    It can handle the following formats:
    - long: varname [(.postfix | idx)...]
    - v[(p|i)...]

    E.g.:
    myvar[2].lo.word
    m2lw
    m<-xl

 */

import { scrCodesCTT, scrTokensTTC, POSTFIX_BY_FULL_READ } from '@zoli8000/gb-consts'

import { NumberTokenizer, defaultNumberEncoder, GBTypeCodes } from './gb-number-encoder'

export const enum NumberType {
    digit,
    byte,
    word,
    fixed08,
    fixed88,
}

export type TypeCodeMapping = Record<NumberType, number[]>

export type VarTokenizerFunction = (text: string) => number[]

export type VarMapping = {
    shortvar: string
    longvar: string
    isArray: boolean
    length: number
}

export type VarEncoderSettings = {
    isStrict: boolean
    dimObject: Record<string, number>
}

export class VarTokenizer {
    subTokenized: string[]
    varMappings: Record<string, VarMapping>
    private validChars: string[]

    constructor(
        public typeCodes: TypeCodeMapping,
        public settings: VarEncoderSettings,
    ) {
        this.subTokenized = []
        this.varMappings = {}

        this.validChars = []
        this.reset()
    }

    reset() {
        this.subTokenized = []
        this.varMappings = {}

        const charLo: string[] = []
        const charHi: string[] = []
        const charExed: string[] = []

        for (let i = 0; i < 26; i++) {
            charLo.push(scrCodesCTT[(i + 1) as keyof typeof scrCodesCTT])
            charHi.push(scrCodesCTT[(i + 65) as keyof typeof scrCodesCTT])
            charExed.push(scrCodesCTT[(i + 65) as keyof typeof scrCodesCTT] + '×')
        }

        this.validChars.push(...charLo, ...charHi, ...charExed)
    }

    subTokenize(text: string, isVerbose: boolean = false): number[] {
        // TODO: fix short for cases like 'al'
        let rx =
            /(?<postindex>\.\d+|<-\d+)|(?<quickindex><-\w+)|(?<postfix>\.\w+)|(?<base>[a-z_A-Z×]+)/gm

        if (!isVerbose) {
            rx =
                /(?<postindex>\.\d+|<-\d+)|(?<quickindex><-\w+)|(?<postfix>\.\w+)|(?<base>[a-zA-Z][×]?)(?<postfix2>[\w×]*)/gm
        }

        rx.lastIndex = 0
        let m = null
        const bytes = []

        while ((m = rx.exec(text)) !== null) {
            if (m.index === rx.lastIndex) {
                rx.lastIndex++
            }

            const base = m.groups?.base || ''
            const postfix = m.groups?.postfix || ''
            const postindex = m.groups?.postindex || ''
            const quickindex = m.groups?.quickindex || ''

            // TODO: short should be pushed and removed from the validChars

            const isShort = !isVerbose || base.length == 1 || (base.length == 2 && base[1] == '×')
            if (base) {
                const shortVar = !isShort ? this.longvarToShortvar(base) : base
                bytes.push(scrTokensTTC[shortVar as keyof typeof scrTokensTTC])

                if (m.groups?.postfix2) {
                    let idx = 0

                    while (idx < m.groups?.postfix2.length) {
                        let char = m.groups?.postfix2[idx]

                        if (m.groups?.postfix2[idx + 1] == '×') {
                            char += '×'
                            idx++
                        }

                        if (char) {
                            bytes.push(scrTokensTTC[char as keyof typeof scrTokensTTC])
                        }
                        idx++
                    }
                }
            }

            if (postfix) {
                const postfixChar =
                    POSTFIX_BY_FULL_READ[
                        postfix.replace(/\./g, '') as keyof typeof POSTFIX_BY_FULL_READ
                    ][0]
                bytes.push(scrTokensTTC[postfixChar as keyof typeof scrTokensTTC])
            } else if (postindex) {
                const postIndexValue = postindex.replace(/<-|\./g, '')
                const numberTokenizer = new NumberTokenizer(GBTypeCodes, defaultNumberEncoder)
                const result = numberTokenizer.integerToArray(postIndexValue)
                bytes.push(...result)
            } else if (quickindex) {
                const quickIndexValue = quickindex.replace(/<-|\./g, '')
                const shortVar = !isShort
                    ? this.longvarToShortvar(quickIndexValue)
                    : quickIndexValue
                bytes.push(scrTokensTTC['<--' as keyof typeof scrTokensTTC])
                bytes.push(scrTokensTTC[shortVar as keyof typeof scrTokensTTC])
            }
        }

        return bytes
    }

    /** Convert long variable name to short */
    longvarToShortvar(text: string): string {
        if (
            this.settings.isStrict &&
            text.length > 0 &&
            this.settings.dimObject[text] === undefined
        ) {
            throw new Error(`Variable ${text} not found in DIM declaration`)
        }

        const shortVar = this.getNextShortVar()
        if (!shortVar) {
            throw new Error(`No more short variable names available`)
        }

        const existingDefinition = this.varMappings[shortVar]
        if (existingDefinition) {
            if (existingDefinition.longvar !== text) {
                throw new Error(
                    `Short variable ${shortVar} already defined as ${existingDefinition.longvar}`,
                )
            }
            return shortVar
        }

        this.varMappings[shortVar] = {
            shortvar: shortVar,
            longvar: text,
            isArray: false,
            length: 0,
        }

        return shortVar
    }

    getNextShortVar(): string {
        return this.validChars.shift() || ''
    }
}

export const defaultVarEncoder: VarEncoderSettings = {
    isStrict: false,
    dimObject: {},
}
