import { GbDirectives } from './languages/gb-preprocessor'
import { SpecialBool } from './special-bool'

export class LanguagePreprocessor {
    directives: Record<string, any> = {}
    directiveValues: string[]
    directiveFunctions: Record<string, () => void>

    constructor(private text: string) {
        this.directiveValues = []
        this.directiveFunctions = {}
    }

    process() {
        this.readGlobalDirectiveValues()
    }

    analyzeVarsFromTokenized(tokens: any[]) {}

    /** Read directives.
     * All lines that start with a # are considered directives if the # is followed by a word without a space.
     * The first line can contain global directives that changes global behavior.
     *
     * The directive is considered valid if it is one of the following:
     * #for: for loop-
     * #console: write to console
     * #start: a block of code that will be executed
     * #end:
     *

    */
    readGlobalDirectiveValues() {
        // Get the first line that starts with a # and is followed directly by a word without a space for directives

        const directiveRx = /(?:^[\n\s]*)((#\S*\s*)+)/m

        const match = this.text.match(directiveRx)

        if (!match) {
            return { result: [], error: null }
        }

        const directivesFound = match[1]
            .split(/\s+/)
            .map((directive: string) => directive.trim().replace(/#/g, ''))
            .filter((directive: string) => directive.length > 0)

        const invalidDirectives: string[] = []
        directivesFound.forEach((directive: string) => {
            if (Object.keys(this.directives).indexOf(directive) === -1) {
                invalidDirectives.push(directive)
            }
        })

        if (invalidDirectives.length > 0) {
            return { result: [], error: `Invalid directive(s): ${invalidDirectives.join(', ')}` }
        }

        return { result: directivesFound, error: null }
    }

    useGlobalDirectives() {
        const directiveResult = this.readGlobalDirectiveValues()
        if (directiveResult.error) {
            throw new Error(directiveResult.error)
        }

        this.directiveValues = directiveResult.result

        this.directiveValues.forEach((directive: string) => {
            this.directiveFunctions[directive]()
        })
    }

    getFinalDirectives(): GbDirectives {
        const transformedDirectives: GbDirectives = {} as GbDirectives

        Object.entries(this.directives).forEach(([key, value]) => {
            if (value instanceof SpecialBool) {
                value = value.isTrue()
            }

            transformedDirectives[key as keyof GbDirectives] = value
        })

        return transformedDirectives
    }
}
