import { LanguageDecoder } from './language-decoder'
import { LanguageEncoder } from './language-encoder'
import { LanguagePreprocessor } from './language-preprocessor'
import { GBModel } from './languages/gb-model'
import { V2Model } from './languages/v2-model'
import { Parser } from './parser'
import { Prettifier } from './prettifier'
import { Tokenizer } from './tokenizer'

export enum LANGUAGE {
    BASIC_V2 = 'BasicV2',
    BASIC_V2_verbose = 'BasicV2-verbose',
    GOLFBAS_short = 'GolfBasic',
    GOLFBAS_verbose = 'GolfBasic-verbose',
}

export type LanguageProps = {
    Tokenizer: typeof Tokenizer
    Prettifier: typeof Prettifier
    Parser: typeof Parser
    Encoder: typeof LanguageEncoder
    Decoder: typeof LanguageDecoder
    Preprocessor: typeof LanguagePreprocessor
}

export const Predefs: Record<string, LanguageProps> = {
    [LANGUAGE.BASIC_V2]: V2Model,
    [LANGUAGE.GOLFBAS_short]: GBModel,
    [LANGUAGE.GOLFBAS_verbose]: GBModel,
}

export class LanguageModel {
    language: string
    model: LanguageProps

    constructor() {
        this.language = 'undefined'
        this.model = {} as unknown as LanguageProps
        return this
    }

    usePredefinedLanguage(language: LANGUAGE) {
        this.language = language
        this.model = Predefs[language]
        return this
    }

    defineLanguage(language: string) {
        this.language = language
        return this
    }
}
