/**
 * 0-9: digits (if digits is allowed)
 * 10..255, -128..-1: byte
 * -32767..65535: word
 * -128.0..256.0  f0.8
 *
 * 3w -> force word
 * 3b -> force byte
 * 3. -> 3.0 as f8.8
 * 3.1f -> as f24.16
 *
 */

import { C64Chr } from './c64-chr'
import { GB } from './gb-consts'

export const enum NumberType {
    digit,
    byte,
    word,
    fixed08,
    fixed88,
}

export type TypeCodeMapping = Record<NumberType, number[]>

export const GBTypeCodes: TypeCodeMapping = {
    [NumberType.digit]: [],
    [NumberType.byte]: [GB.TTC_COMMAND_TOKENS['BYTE']],
    [NumberType.word]: [GB.TTC_COMMAND_TOKENS['WORD']],
    [NumberType.fixed08]: [GB.TTC_COMMAND_TOKENS['F08']],
    [NumberType.fixed88]: [GB.TTC_COMMAND_TOKENS['F88']],
}

export type NumberTokenizerFunction = (text: string) => number[]

export type NumberEncoderSettings = {
    allowDigits: boolean
    allowTokens: boolean
    integerEncoders: NumberTokenizerFunction[]
}

export class NumberTokenizer {
    constructor(
        public typeCodes: TypeCodeMapping,
        public settings: NumberEncoderSettings,
    ) {}

    /** Convert to digit */
    digitToArray(text: string): number[] {
        const byteValue = parseInt(text)

        if (byteValue < 0 || byteValue > 9) {
            throw TypeError(`Invalid digit for byte parsing: ${text}`)
        }

        return [C64Chr.textToScreen(text)]
    }

    /** Convert byte */
    byteToArray(text: string): number[] {
        const byteValue = parseInt(text)

        if (byteValue < -128 || byteValue > 255) {
            throw TypeError(`Invalid value for byte parsing: ${text}`)
        }

        return [...this.typeCodes[NumberType.byte], (byteValue + 0x100) & 0xff]
    }

    /** Convert byte */
    wordToArray(text: string): number[] {
        const numberValue = parseInt(text)

        if (numberValue < -32768 || numberValue > 65535) {
            throw TypeError(`Invalid value for word parsing: ${text}`)
        }

        const wordValue = (numberValue + 0x10000) & 0xffff

        return [...this.typeCodes[NumberType.word], wordValue & 0xff, wordValue >> 8]
    }

    integerToArray(text: string): number[] {
        const _intValue = parseInt(text)

        const bytes = this.settings.integerEncoders
            .map((encoderFn) => {
                try {
                    return encoderFn.bind(this)(text)
                } catch {
                    return []
                }
            })
            .filter((value) => value.length > 0)[0]

        return bytes
    }
}

export const defaultNumberEncoder: NumberEncoderSettings = {
    allowDigits: true,
    allowTokens: true,
    integerEncoders: [
        NumberTokenizer.prototype.digitToArray,
        NumberTokenizer.prototype.byteToArray,
        NumberTokenizer.prototype.wordToArray,
    ],
}
