export class C64Chr {
    static screenCodeToChar(screenCode: number, small: boolean = false): [string, boolean] {
        if (small) {
            return [
                '@ABCDEFGHIJKLMNOPQRSTUVWXYZ[£]↑← !"#$%&\'()*+,-./0123456789:;<=>?━♠│─▔▔▁▍▕╮╰╯└╲╱┌┐●▁♥▏╭╳○♣▕♦➕⡇|.. ▌▄‾▁▎▒▕⣤.▕┣▗┗┓▂┏┻┳┫▎▍▐‾▀▃.▖▝┛▘▚'[
                    screenCode & 0x7f
                ],
                (screenCode & 0x80) > 0,
            ]
        } else {
            return [
                '@abcdefghijklmnopqrstuvwxyz[£]↑← !"#$%&\'()*+,-./0123456789:;<=>?━ABCDEFGHIJKLMNOPQRSTUVWXYZ➕⡇|π◥ ▌▄‾▁▎▒▕⣤◤▕┣▗┗┓▂┏┻┳┫▎▍▐‾▀▃⅃▖▝┛▘▚'[
                    screenCode & 0x7f
                ],
                (screenCode & 0x80) > 0,
            ]
        }
    }
    static petsciiCodeToScreen(petsciiCode: number) {
        let screenCode = 0

        if (petsciiCode < 32) screenCode = petsciiCode + 128
        else if (32 <= petsciiCode && petsciiCode < 64) screenCode = petsciiCode
        else if (64 <= petsciiCode && petsciiCode < 96) screenCode = petsciiCode - 64
        else if (96 <= petsciiCode && petsciiCode < 128) screenCode = petsciiCode - 32
        else if (128 <= petsciiCode && petsciiCode < 160) screenCode = petsciiCode + 64
        else if (160 <= petsciiCode && petsciiCode < 192) screenCode = petsciiCode - 64
        else if (192 <= petsciiCode && petsciiCode < 224) screenCode = petsciiCode - 128
        else if (224 <= petsciiCode && petsciiCode < 255) screenCode = petsciiCode - 128
        else if (petsciiCode == 255) screenCode = 94

        return screenCode
    }

    static textToPetscii(asciiCode: string): number {
        let code = asciiCode.charCodeAt(0)

        // PETSCII and ASCII has swapped upper and lower case
        if ((0x41 <= code && code <= 0x5a) || (0x61 <= code && code <= 0x7a)) {
            code ^= 0x20
        }

        return code
    }

    // ToDo: better converting statics!!!

    static textToScreen(asciiCode: string): number {
        const code = C64Chr.textToPetscii(asciiCode)
        return C64Chr.petsciiCodeToScreen(code)
    }

    static petsciiCodeToChar(petsciiCode: number, small: boolean = false): [string, boolean] {
        return C64Chr.screenCodeToChar(C64Chr.petsciiCodeToScreen(petsciiCode), small)
    }

    static screenCodeToEscaped(screenCode: number, small: boolean = false): [string, any] {
        if (small) {
            throw Error('Small character conversion not implemented!')
        }
        if (screenCode == 64 || screenCode >= 91) {
            return ['code', screenCode]
        } else {
            return [
                'chr',
                '@abcdefghijklmnopqrstuvwxyz[£]↑← !"#$%&\'()*+,-./0123456789:;<=>?━ABCDEFGHIJKLMNOPQRSTUVWXYZ'[
                    screenCode
                ],
            ]
        }
    }

    static petsciiCodeToEscaped(petsciiCode: number, small: boolean = false): [string, any] {
        const screenCode = C64Chr.petsciiCodeToScreen(petsciiCode)
        const result = C64Chr.screenCodeToEscaped(screenCode, small)
        if (result[0] == 'code') result[1] = petsciiCode

        return result
    }

    static petsciiCodeToSafe(petsciiCode: number, small: boolean = false): string {
        const [mode, val] = C64Chr.petsciiCodeToEscaped(petsciiCode, small)
        if (mode == 'chr') return val
        return '?'
    }
}
