import { Token } from './token'
import { TokenArray } from './token-array'

export abstract class Prettifier {
    /** Prettify a token based on its type and value. (E.g. add spaces before and after.) */
    static prettifyToken(_type: string, _category: string, _value: string): string {
        return ''
    }

    /** Get indentation offset from the current type and value.
     * Returns: [offsetThisLine, offsetNextLine]
     */
    static getIndentOffset(_type: string, _value: string): [number, number] {
        return [0, 0]
    }

    /** How many new lines to be added based on the new token
     * (e.g. whitespace block with multiple enters)
     *
     * @param token The current Token
     * @param array The complete Token array
     * @param idx   The current index in the array
     *
     * Returns Token: token to be displayed (usually null)
     *          number: number of empty lines to be added (0: no line-break, 1: continue in next line etc.)
     * */
    static getNewLineCountFunction(
        token: Token,
        _array: TokenArray,
        _idx: number,
    ): [Token | null, number] {
        return [token, 0]
    }
}

export class BasePrettifier extends Prettifier {
    static prettifyToken(_type: string, _category: string, value: string): string {
        return value
    }

    static getIndentOffset(_type: string, _value: string): [number, number] {
        return [0, 0]
    }

    static getNewLineCountFunction(
        token: Token,
        _array: TokenArray,
        _idx: number,
    ): [Token | null, number] {
        if (token.key !== 'whiteSpc') return [token, 0]

        return [null, token.value.match(/\r\n|\n|\r/g)?.length || 0]
    }
}
