import {
    COMMANDS_BY_NAME,
    GSCR_CODES,
    GSPR_CODES,
    FONT_CMD_TTC,
    TOKENS_BY_CODE,
    TOKENS_BY_NAME,
    TOK_PROP,
} from '@zoli8000/gb-consts'

export class GB {
    static readonly CTT_COMMAND_TOKENS: Record<number, any> = TOKENS_BY_CODE
    static readonly TTC_COMMAND_TOKENS: Record<string, any> = Object.fromEntries(
        Object.entries(TOKENS_BY_NAME).map(([key, value]) => [key, value[0]]),
    )

    static readonly TTC_SCREEN_TOKENS: Record<string, number> = {
        ...GSCR_CODES,
        ...GSPR_CODES,
        ...FONT_CMD_TTC,
    }

    static readonly TTC_STRING_TOKENS: Record<string, number> = GB.TTC_SCREEN_TOKENS

    static readonly TTC_STRING_TOKENS_UPPER: Record<string, number> = Object.fromEntries(
        Object.entries(GB.TTC_STRING_TOKENS).map(([key, value]) => [key.toUpperCase(), value]),
    )

    static readonly CTT_STRING_TOKENS: Record<string, string> = Object.fromEntries(
        Object.entries(GB.TTC_STRING_TOKENS)
            .filter(([key, _value]) => key.indexOf('spr_') !== 0)
            .map(([key, value]) => [value, key]),
    )

    static readonly EXTRA_ZEROS: Record<string, number> = Object.fromEntries(
        Object.entries(TOKENS_BY_NAME).map(([key, value]) => {
            let bytes = 0
            if (
                value[1] & TOK_PROP.COMMAND &&
                value[1] & (TOK_PROP.NEXT_ADDR | TOK_PROP.BRANCH_ADDR)
            ) {
                if (value[1] & TOK_PROP.NEXT_ADDR) {
                    bytes++
                }
                if (value[1] & TOK_PROP.BRANCH_ADDR) {
                    bytes++
                }
            }
            return [key, bytes * 2]
        }),
    )

    static readonly IS_COMMAND: Record<string, boolean> = Object.fromEntries(
        Object.entries(TOKENS_BY_NAME).map(([key, value]) => {
            return [key, value[1] & TOK_PROP.COMMAND ? true : false]
        }),
    )
    static readonly CTT_SPRITE_TOKENS: Record<string, string> = Object.fromEntries(
        Object.entries(GB.TTC_STRING_TOKENS)
            .filter(([key, _value]) => key.indexOf('spr_') == 0)
            .map(([key, value]) => [value, key]),
    )

    static blockEndToken = this.TTC_COMMAND_TOKENS._lf
    static finalToken = this.TTC_COMMAND_TOKENS.end

    static strDQuote = this.TTC_COMMAND_TOKENS.DQUOTE
    static strSQuote = this.TTC_COMMAND_TOKENS.SQUOTE
    static strBQuote = this.TTC_COMMAND_TOKENS.SQUOTE
    static strQQuote = this.TTC_COMMAND_TOKENS.QQUOTE

    static cmdParams = { ...COMMANDS_BY_NAME }

    static gbFuncs = {
        SETPOS(x: string, y: string): number[] {
            const pos = parseInt(x, 10) + parseInt(y, 10) * 40
            return [GB.TTC_SCREEN_TOKENS.set_addr0 + (pos >> 8), pos & 0xff]
        },
    }
}
