import { Token } from './token'
import { TokenArray } from './token-array'
import { TokenizerSettings } from './tokenizer-settings'

/** Tokenizer definitions.
 * Records: key: [orderId, regexp pattern]
 */
export type TokenizerDefinition = Record<string, [number, string]>

/** A string, Regexp or a function returning the final string */
export type TokenizerFunction = string | RegExp | ((key: string, value: string) => [string, string])

export enum TokenBuilderItem {
    remark = 'remark',
    docstr = 'docstr',
    remMulti = 'remMulti',
    strDQuote = 'strDQuote',
    strSQuote = 'strSQuote',
    strBQuote = 'strBQuote',
    whiteSpc = 'whiteSpc',
    numHex = 'hexNum',
    numFloat = 'floatNum',
    numInt = 'intNum',

    identifier = 'identifier',

    other = 'other',
}

const rxTokenConsts: TokenizerDefinition = {
    // Remarks
    remark: [1100, String.raw`(?<remark>\/\/.*$)`],
    remDash: [1150, String.raw`(?<remark>[\-]{2}.*$)`],
    docstr: [1200, String.raw`(?<docstr>\/[*]{2}[\S\s]*?[*]\/)`],
    remMulti: [1300, String.raw`(?<rem_multi>\/[*][\S\s]*?[*]\/)`],

    // Strings
    strDQuote: [2100, String.raw`(?<other__strDO>\")(?<strDQuote>[^\"]*?)(?<other__strDC>\")`],
    strSQuote: [2200, String.raw`(?<other__strSO>\')(?<strSQuote>[^\']*?)(?<other__strSC>\')`],
    strBQuote: [2300, String.raw`(?<other__strBO>\`)(?<strBQuote>[^\`]*?)(?<other__strBC>\`)`],

    // Whitespace
    whiteSpc: [3100, String.raw`(?<whiteSpc>\s+)`],

    // Numbers:
    hexNum: [4100, String.raw`(?<hexNum>(0x[0-9a-f]+))`],
    floatNum: [4200, String.raw`(?<floatNum>(\d*[.]\d*))`],
    intNum: [4300, String.raw`(?<intNum>(\d+))`],

    // Identifier:
    identifier: [8100, String.raw`(?<identifier>[\w.]+)`],
}

/** Tokenizer class
 *
 */
export class Tokenizer {
    /** Settings */
    settings: TokenizerSettings

    /** Regexp used for tokenizing. */
    rxPattern: RegExp

    /** Subregexps for formatting only. */
    subFormatPatterns: Record<string, TokenizerFunction>

    /** Raw input text */
    rawText: string

    /* Token class */
    TokenClass: typeof Token

    /**  Output: tokens */
    tokens: TokenArray

    /** Helper table for determining line and position from character number */
    linePosTable: { start: number; end: number }[]

    /** Helper string for checking possible character leakage */
    tokenizedRaw: string

    /** Creates a new Tokenizer.
     * @param settings     A TokenizerSettings instance with settings for the tokenizer.
     */
    constructor(settings: TokenizerSettings) {
        if (!settings.TokenClass || !settings.splitFunc || !settings.rxPattern) {
            throw Error('Invalid TokenizerSettings object. Use class to create one!')
        }

        this.settings = settings
        this.TokenClass = this.settings.TokenClass
        this.rxPattern = new RegExp(this.settings.rxPattern, 'gm')

        if (Object.keys(settings.subFormatPatterns).length > 0) {
            this.subFormatPatterns = Object.fromEntries(
                Object.entries(settings.subFormatPatterns).map(([key, value]) => [
                    key,
                    Tokenizer.convertTokenizerFunction(value),
                ]),
            )
        } else {
            this.subFormatPatterns = {}
        }

        this.rawText = ''
        this.tokens = new TokenArray()
        this.linePosTable = []
        this.tokenizedRaw = ''
    }

    reset() {
        this.tokens = []
        this.linePosTable = []
    }

    static convertTokenizerFunction(tokenizerFunction: TokenizerFunction): TokenizerFunction {
        if (typeof tokenizerFunction == 'string') {
            return Tokenizer.verboseRegexp(tokenizerFunction, 'gm')
        }

        return tokenizerFunction
    }

    static verboseRegexp(regexpStr: string, flags: string): RegExp {
        const rx = /(?<!\\)\s|[/][/].*|[/][*][\s\S]*[*][/]/g
        const result = regexpStr.replace(rx, '')

        return new RegExp(result, flags)
    }

    /** Builds a regexp from building blocks
     *
     * @param items: regexp group names, can be TokenBuilderItem values and keys from extraDict
     * @param extraDict additional regexp items
     * @param guard: string for adding guards for uncaptured text. Default value captures each single character. Use '' to disable and use _guard blocks instead during tokenization.
     * @returns regexp string
     */
    static buildRxString(
        items: string[],
        extraDict: TokenizerDefinition | null = null,
        guard: string = '(?<other>.)',
    ) {
        const rxItems: [number, string][] = []

        items.forEach((item) => {
            let value = extraDict?.[item]

            if (!value) value = rxTokenConsts[item]

            if (!value) {
                throw Error(`Invalid item: ${item}`)
            }
            rxItems.push(value)
        })

        const orderedList = rxItems.sort((a, b) => a[0] - b[0]).map((a) => a[1])
        if (guard) orderedList.push(guard)

        const rxRawString = orderedList.join(' | ')
        return Tokenizer.verboseRegexp(rxRawString, 'gm')
    }
    static noSplitter(
        token: Token,
        _settings: TokenizerSettings,
        _TokenClass: typeof Token,
    ): Token[] {
        return [token]
    }

    static lineSplitter(
        token: Token,
        settings: TokenizerSettings,
        TokenClass: typeof Token,
    ): Token[] {
        const tokenValues = token.value.split(/\r\n|\r|\n/gm)

        if (tokenValues.length == 1) return [token]
        const tokens: Token[] = []

        tokenValues.forEach((tokenValue: string, index: number) => {
            if (index) {
                const newLine = Tokenizer.createToken(
                    'addNewLine',
                    '',
                    token.pos,
                    settings,
                    TokenClass,
                )
                tokens.push(newLine)
            }

            const ofs = 0
            const position = token.pos + ofs
            const newToken = Tokenizer.createToken(
                token.key,
                tokenValue,
                position,
                settings,
                TokenClass,
            )
            tokens.push(newToken)
        })
        return tokens
    }

    getRxString(): string {
        return this.rxPattern.toString()
    }

    /** Add a token to the tokenized list */
    addToken(key: string, value: string, position: any) {
        const t = Tokenizer.createToken(key, value, position, this.settings, this.TokenClass)
        this.tokens.push(t)
    }

    static createToken(
        key: string,
        value: string,
        position: any,
        settings: TokenizerSettings,
        TokenClass: typeof Token,
    ): Token {
        const category = settings.mapper[key] || key
        const spanClass = settings.htmlClassPrefix + category

        const repr = value
        const reprPretty = settings.PrettifierClass.prettifyToken(key, category, repr)

        const t = new TokenClass(
            key,
            value,
            position,
            repr,
            reprPretty,
            category,
            spanClass,
            settings.tokenTypeMapper,
        )

        return t
    }

    /** Position of the last token */
    get currentPos() {
        const pos = this.tokens[this.tokens.length - 1].pos
        return this.posToString(pos)
    }

    /** Tokenize the raw string */
    tokenizeString(rawText: string) {
        this.rawText = rawText
        this.reset()
        this.tokenizedRaw = ''

        this.collectLineInfo()
        let result: RegExpMatchArray

        this.rxPattern.lastIndex = 0
        let lastPos = -1
        let lastLen = 1

        while ((result = this.rxPattern.exec(this.rawText) as RegExpMatchArray)) {
            let key, value
            if (!result.groups) {
                throw Error('Use named capturing blocks!')
            }

            const resultList = Object.entries(result.groups).filter(([_k, v]) => v != undefined)

            const capturedLength = Object.values(resultList).reduce((t, c) => t + c[1].length, 0)

            if (capturedLength !== result[0].length) {
                throw Error(`Use named capturing blocks - not captured completely: ${result[0]}`)
            }

            if (result.index == lastPos) {
                throw Error(
                    'Invalid regexp - do not use elements which can return empty values, e.g. (?<value>.*) !',
                )
            }

            if (result.index != lastPos + lastLen) {
                const missingValue = rawText.slice(lastPos + lastLen, result.index)

                if (lastPos == -1) lastPos = 0

                this.addToken('_guard', missingValue, lastPos)
                this.tokenizedRaw += missingValue
            }

            resultList.forEach((resultItem) => {
                ;[key, value] = resultItem as [string, string]

                key = key.replace(/__.*$/, '')

                if (!this.settings.skipEmpty || value) {
                    this.addToken(key, value, result.index)
                    this.tokenizedRaw += value
                }
            })

            lastPos = result.index || 0
            lastLen = result[0].length || 0
        }

        if (lastPos + lastLen < rawText.length) {
            const missingValue = rawText.slice(lastPos + lastLen)
            this.addToken('_guard', missingValue, lastPos)
            this.tokenizedRaw += missingValue
        }

        if (this.tokenizedRaw.length != rawText.length) {
            throw Error('Invalid regexp - characters are missing!')
        }
    }

    /** Creates the linePosTable list for determining positions. */
    collectLineInfo() {
        this.linePosTable = []
        const lines = this.rawText.split('\n')

        let lineStart = 0
        let lineEnd = 0

        lines.forEach((line: string) => {
            lineEnd = lineStart + line.length
            const lineData = { start: lineStart, end: lineEnd }
            this.linePosTable.push(lineData)

            lineStart = lineEnd + 1
        })
    }

    /** Returns a line and column number from the position. */
    getLineInfo(pos: number): [number, number] {
        let lineEndPos = -1
        let line = -1

        while (pos > lineEndPos) {
            line++
            lineEndPos = this.linePosTable[line].end
        }

        return [line, this.linePosTable[line--].start]
    }

    /** String representation of a position.
     * @param pos       Position
     * @param formatStr Template string. Possible values: %ln: line, %col: column, %pos: position.
     * @param ofs       Starting index for columns and lines. (Default: +1. Use 0 for 0-based line and column numbers.)
     *
     */
    posToString(pos: number, formatStr: string = 'ln %ln col %col', ofs = 1): string {
        const [line, lineStartPos] = this.getLineInfo(pos)
        const char = pos - lineStartPos
        const posString = formatStr
            .replace('%ln', (line + ofs).toString())
            .replace('%pos', pos.toString())
            .replace('%col', (char + ofs).toString())
        return posString
    }

    /*
    toString() {
        const str = this.tokens.map((t: Token) =>
            `<${t.key} p='${t.pos}' c='${t.category}'>${t.value}</${t.key}>\n`
        ).join('')

        return str
    }

    toFormattedHtml() {
        const str = this.tokens.map((t: Token) =>
            `<span class='${t.spanClass}'>${t.value}</span>\n`
        ).join('')

        return str

    }
    */

    static getNewLines(t: Token): number {
        if (t.key !== 'whiteSpc') return 0

        return t.value.match(/\r\n|\n|\r/g)?.length || 0
    }

    getFormattedFromTokens(_field: 'value' | 'repr' | 'reprPretty' = 'reprPretty') {
        const tokenData: TokenArray = this.tokens
        const allFormattedHTML: string[] = ['']
        const allTexts: string[] = ['']

        const allValues: string[] = ['']
        const allRepr: string[] = ['']
        const allReprPretty: string[] = ['']

        tokenData.forEach((t: Token, i: number, a: any[]) => {
            const [token, newLines]: [Token | null, number] =
                this.settings.PrettifierClass.getNewLineCountFunction(t, a, i)

            if (newLines) addNewLines(newLines)
            else if (token) {
                const splittedTokens = this.settings.splitFunc(
                    token,
                    this.settings,
                    this.TokenClass,
                )

                splittedTokens.forEach((splittedToken: Token, _index: number) => {
                    if (splittedToken.key == 'addNewLine') {
                        addNewLines(1)
                    } else {
                        if (splittedToken.value != '' || !this.settings.skipEmpty)
                            addToken(splittedToken, this)
                    }
                })
            }

            function addNewLines(count: number) {
                for (let i = 0; i < count; i++) {
                    allFormattedHTML.push('')
                    allTexts.push('')
                    allValues.push('')
                    allRepr.push('')
                    allReprPretty.push('')
                }
            }

            function getHtmlFormat(
                arr: any[],
                key: string,
                value: string,
                rxs: Record<string, TokenizerFunction>,
                settings: TokenizerSettings,
            ) {
                if (key in rxs) {
                    const rx = rxs[key]

                    if (typeof rx == 'function') {
                        const [newKey, newValue] = rx(key, value)

                        getHtmlFormat(arr, newKey, newValue, rxs, settings)
                        return arr
                    }

                    if (!(rx instanceof RegExp)) {
                        throw Error('Not a regexp value!')
                    }
                    let match: RegExpMatchArray | null

                    rx.lastIndex = 0
                    let textUsedLength = 0

                    let _textUsed = ''
                    let lastValue = 0

                    function addError(sliceFrom: number, sliceTo: number) {
                        const miss = value.slice(sliceFrom, sliceTo)
                        textUsedLength += miss.length
                        _textUsed += miss
                        getHtmlFormat(arr, 'error', miss, rxs, settings)
                    }

                    while ((match = rx.exec(value))) {
                        if (
                            !match ||
                            !match.groups ||
                            !match[0].length ||
                            match.index == undefined
                        ) {
                            throw Error('Update regexp - empty result!')
                        }

                        Object.entries(match.groups!).forEach(([subKey, subValue]) => {
                            if (!subValue) return

                            textUsedLength += subValue.length
                            _textUsed += subValue
                            getHtmlFormat(arr, subKey, subValue, rxs, settings)
                        })

                        if (match.index != lastValue) addError(lastValue, match.index)

                        lastValue = match.index + match[0].length
                    }

                    if (lastValue != value.length) addError(lastValue, value.length)

                    if (textUsedLength != value.length) {
                        throw Error('Update regexp - characters are missing!')
                    }

                    return arr
                } else {
                    const category =
                        settings.mapper[key.replace(/__.*$/, '')] || key.replace(/__.*$/, '')
                    const spanClass = settings.htmlClassPrefix + category

                    const htmlString = value.replace(/ /g, String.fromCharCode(160))
                    const splitted = htmlString.split('\n')

                    splitted.forEach((splitHtmlText, index) => {
                        if (index > 0) {
                            addNewLines(1)
                        }
                        if (splitHtmlText.length > 0) {
                            const element = `<span class='${spanClass}'>${splitHtmlText}</span>`
                            allFormattedHTML[allFormattedHTML.length - 1] += element
                            allTexts[allTexts.length - 1] += splitHtmlText //t[field]
                            allValues[allValues.length - 1] += splitHtmlText //t.value
                            allRepr[allRepr.length - 1] += splitHtmlText //t.repr
                            allReprPretty[allReprPretty.length - 1] += splitHtmlText //t.reprPretty
                        }
                    })

                    return arr
                }
            }

            function addToken(t: Token, self: Tokenizer) {
                if (token != null) {
                    const newArr = [[]]
                    getHtmlFormat(newArr, t.key, t.value, self.subFormatPatterns, self.settings)
                }
            }
        })

        return { allFormattedHTML, allTexts, allRepr, allReprPretty, allValues }
    }
}

