import { ParserSettings } from './parser-settings'
import { Token } from './token'
import { TokenArray } from './token-array'

/*
type ParserCallable = () => void
type ParserDefinition = Record<string, string>

const parserDefinitions: ParserDefinition = {
    // Remarks
    digit: '[0-9]',
    letter: '[a-zA-Z]',
    word: 'letter | [_]',
    identifier: 'word, {word | digit}',
    vartype:
    var: '<varName: identifier> [:] <varType: vartype>'
    main:   [
    docstr:   [1200, String.raw`(?<docstr>\/[*]{2}[\S\s]*?[*]\/)`],
    remMulti: [1300, String.raw`(?<rem_multi>\/[*][\S\s]*?[*]\/)`],
}
*/

export class Parser {
    /** Token array containing tokenized values */
    tokens: TokenArray = []

    /** Index of the current token */
    activeTokenId: number = 0

    /** Active token */
    token: Token = {} as unknown as Token
    nextToken: Token = {} as unknown as Token

    scope: string = ''
    docStr: Record<string, string> = {}

    params: Record<string, any> = {}
    tags: string[] = []

    /** Labels found in text */
    labels: Record<string, any> = {}

    /** Funcions found */
    functions: Record<string, any> = {}

    constructor(public settings: ParserSettings) {}

    parseTokens(tokens: TokenArray) {
        this.tokens = tokens

        this.activeTokenId = 0
        this.token = { key: '_whsp' } as Token
        this.params = { vars: {} }
        this.docStr = {}
        this.scope = '__global'
        this.tags = []
        this.labels = {}
        this.functions = {}
    }

    isFinished() {
        return this.activeTokenId >= this.tokens.length
    }

    posToString() {
        return this.settings.posToStringFn(this.token.pos)
    }

    startTag(tag: string, value: string) {
        const spaces = '   '.repeat(this.tags.length)
        this.tags.push(tag)
        console.log(`${spaces}<${tag}> ${value}`)
    }

    endTag() {
        const tag = this.tags.pop()
        const spaces = '   '.repeat(this.tags.length)
        console.log(`${spaces}</${tag}>`)
    }

    advance() {
        if (this.isFinished()) {
            throw Error('Document finished unexpectedly!')
        }
        do {
            this.token = this.tokens[this.activeTokenId]
            this.activeTokenId++

            if (this.token.key == this.settings.keys['_docstr']) {
                this.docStr[this.scope] = (this.docStr[this.scope] || '') + this.token.value
            }

            const tempIdx = this.activeTokenId

            do {
                this.nextToken = this.tokens[this.activeTokenId]
                this.activeTokenId++
            } while (
                (this.nextToken.key == this.settings.keys['_whsp'] || this.nextToken.isRemark()) &&
                !this.isFinished()
            )

            this.activeTokenId = tempIdx
            if (this.nextToken == undefined) break
        } while (
            (this.token.key == this.settings.keys['_whsp'] || this.token.isRemark()) &&
            !this.isFinished()
        )
    }

    unexpectedToken(what: string) {
        throw Error(
            `Unexpected token '${this.token.value}' at ${this.posToString()}. \nExpected: ${what}.`,
        )
    }

    eatGivenType(key: string) {
        this.advance()

        if (this.token.key !== key) {
            this.unexpectedToken(key)
        }
    }

    eatIdentifier() {
        this.eatGivenType('identifier')
        return this.token
    }

    eatValue(what: string) {
        this.advance()

        if (this.token.value !== what) {
            this.unexpectedToken(what)
        }
    }

    eatAnyOfList(list: string[]) {
        this.advance()

        if (list.indexOf(this.token.value) == -1 && list.indexOf(this.token.key) == -1) {
            this.unexpectedToken(list.toString())
        }
    }

    eatMany(whatsExpr: string): Record<string, Token> {
        const returnTokens: Record<string, any> = {}
        const rxExpr =
            /([{]{2}(?<inputType>\w+)\s+(?<inputName>\w+)[}]{2})|(?<const>\S+)|(?<whitespace>\s+)/gm

        let match: RegExpMatchArray | null = null

        while ((match = rxExpr.exec(whatsExpr)) !== null) {
            if (match.groups === undefined) continue

            if (match.groups.const !== undefined) {
                this.eatValue(match.groups.const)
            } else if (match.groups.inputType !== undefined) {
                this.eatGivenType(match.groups.inputType)
                returnTokens[match.groups.inputName as keyof typeof returnTokens] = this.token
            }

            if (this.isFinished()) {
                throw Error(`Unfinished expression, expected ${whatsExpr}`)
            }
        }

        return returnTokens
    }

    parseVarDeclaration() {
        const varName = this.eatIdentifier().value
        this.eatValue(':')
        const varType = this.eatIdentifier().value

        this.params['vars'][varName] = varType
    }

    parseClassConst() {
        this.parseConst()
    }

    parseParams() {
        let paramName = ''
        let paramType = ''
        const params: Record<string, any> = {}

        while (true) {
            this.eatAnyOfList(['identifier', ')'])

            if (this.token.value == ')') {
                break
            }

            paramName = this.token.value
            paramType = 'any'
            this.eatAnyOfList([':', ',', ')'])

            if (this.token.value == ':') {
                paramType = this.eatIdentifier().value
                this.eatAnyOfList([',', ')'])
            }

            params[paramName] = paramType
            if (this.token.value == ')') break
        }

        return params
    }

    parseExpression() {
        this.startTag('expression', '')

        while (true) {
            this.advance()

            // ( - ~
            if (this.token.isUnaryOperation()) {
                this.advance()
            }

            if (this.token.isNumber()) {
                null // eslint-disable-line @typescript-eslint/no-unused-expressions
            } else if (this.token.isString()) {
                null // eslint-disable-line @typescript-eslint/no-unused-expressions
            } else {
                break
            }

            if (this.nextToken.isBinaryOperation()) {
                this.advance()
            }
        }
        this.endTag()
    }

    parseDict() {
        this.eatValue('{')
        while (this.token.value != '}') {
            this.advance()
        }
    }

    parseList() {
        this.eatValue('[')
        let brackets = 1

        while (brackets != 0) {
            this.advance()

            if (this.token.value == '[') brackets++
            if (this.token.value == ']') brackets--
        }
    }

    parseConst() {
        const constName = this.eatIdentifier().value
        this.eatValue('=')

        this.startTag('const', constName)

        if (this.nextToken.value == '{') {
            this.parseDict()
        } else if (this.nextToken.value == '[') {
            this.parseList()
        } else {
            this.parseExpression()
        }

        this.endTag()
    }

    parseBlock() {
        this.startTag('block', '')
        while (true) {
            const savedPos = this.activeTokenId
            this.advance()
            if (this.token.value == '}') {
                this.activeTokenId = savedPos
                break
            }

            if (this.token.key == 'keyword') {
                switch (this.token.value) {
                    case 'const':
                        this.parseConst()
                }
            }
        }
        this.endTag()
    }

    parseMethod() {
        const identifier = this.eatIdentifier().value

        this.startTag('method', identifier)

        this.params['method'] = identifier

        this.eatValue('(')
        this.parseParams()

        this.eatValue('{')
        this.parseBlock()
        this.eatValue('}')

        this.endTag()
    }

    parseMethods() {
        while (true) {
            this.parseMethod()

            if (this.nextToken.value == '}') break
        }
    }
    parseClass() {
        const identifiers = this.eatMany('class {{identifier classname}} extends GolfClass {')
        const className = identifiers.classname.value
        this.params['class'] = className

        this.startTag('class', className)

        while (true) {
            const savePos = this.activeTokenId
            this.advance()
            this.activeTokenId = savePos

            if (this.token.value == '}') break

            switch (this.nextToken.value) {
                case ':':
                    this.parseVarDeclaration()
                    break

                case '=':
                    this.parseClassConst()
                    break

                case '(':
                    this.parseMethod()
                    break
            }
        }

        this.eatValue('}')
        this.endTag()
    }
}
