import { Prettifier } from '../prettifier'
import { Token } from '../token'
import { TokenArray } from '../token-array'

export class V2Prettifier extends Prettifier {
    static PRETTIFY_REPL: Record<string, string> = {
        '=': ' = ',
        '+': ' + ',
        '-': ' - ',
        '*': ' * ',
        '/': ' / ',
        ',': ', ',
        to: ' to ',
        step: ' step ',
        and: ' and ',
        or: ' or ',
        fn: 'fn ',
        then: ' then ',
    }

    static prettifyToken(type: string, category: string, value: string): string {
        if (type !== 'func' && type !== 'command' && type !== 'other') return value

        if (type == 'command') return value + ' '

        return V2Prettifier.PRETTIFY_REPL[value] || value
    }

    static getIndentOffset(_type: string, _value: string): [number, number] {
        return [0, 0]
    }

    static getNewLineCountFunction(
        token: Token,
        _array: TokenArray,
        _idx: number,
    ): [Token | null, number] {
        if (token.key !== 'newLines') return [token, 0]

        return [null, token.value.match(/\r\n|\n|\r/g)?.length || 0]
    }
}

export class V2PrettifierOneCommand extends V2Prettifier {
    static getNewLineCountFunction(
        token: Token,
        _array: TokenArray,
        _idx: number,
    ): [Token | null, number] {
        if (token.key !== 'whiteSpc' && token.key !== 'commandSep') return [token, 0]

        return [null, token.value.match(/\r\n|\n|\r|:/g)?.length || 0]
    }
}
