export class StringTokenizer {
    static getGroups(match: RegExpMatchArray): Record<string, string> {
        const groups: Record<string, string> = {}

        if (match === undefined || match?.groups === undefined) {
            // console.log("Empty.")
            return {}
        }

        Object.keys(match.groups).forEach((key) => {
            if (match.groups && match.groups[key] !== undefined && match.groups[key] !== '') {
                const value = match.groups![key]
                groups[key] = value
            }
        })
        return groups
    }

    static verboseRegexp(regexpStr: string, flags: string): RegExp {
        const rx = /(?<!\\)\s|[/][/].*|[/][*][\s\S]*[*][/]/g
        const result = regexpStr.replace(rx, '')

        return new RegExp(result, flags)
    }

    static STRING_PARSER = StringTokenizer.verboseRegexp(
        String.raw`
         (\s*?)(
          (?<rem>(([-]{2}|[/]{2}).*$)|[/][*][\s\S]*?[*][/]) |

          ( (?<str_extra_fn>\w+\s*)[(]
            (?<str_extra_fnparam>[^)][^)]*?)
            (?<str_extra_end>[)])
          ) |

          (
            (?<code>[a-zA-Z_][a-zA-Z_0-9]*) |
            (?<hexa>(?:[$]|0x)[0-9a-fA-F]+[w]{0,1}) |
            (?<deci>[0-9]+[w]{0,1})

          )?
          (?:[*]?)
          (?<multi>[0-9]+)?
          (?<sep>[,;]\s*|$|\s+)?
        )
        `,
        'gm',
    )

    /** Converts special text to bytes.
     * Returns:
     *  {byteArray, status, invalidCodes}
     *   * byteArray: result.
     *    * status: OK / ERROR
     *    * invalidCodes: array containing errors
     *
     *   Props:
     *    * tokens: <token name - code number> Records
     *    * funcs:  <function name - function> Records
     *    * worldLH: boolean, default=Lo.HI. LO.HI or HI.LO order
     *    * useUppercase: use uppercase or not for codes - default: not. Use this with uppercase token dict to make case insensitive
     *    * normalConverter: function to convert characters, default: String.charCodeAt
     */
    static convertSpecToBytes(str: string, props: any): Record<string, any> {
        const tokens: Record<string, number> = props.tokens || {}
        const funcs: Record<string, any> = props.funcs || {}
        const wordLH: boolean = props.worldLH === undefined ? true : props.worldLH
        const _useUppercase: boolean = props.useUppercase === undefined ? false : props.useUppercase

        const byteArray: number[] = []

        let match: RegExpExecArray | null

        // Remove single and multiline remarks
        // str = str.replace(/[/][/].*$|[/][*].*?[*][/]/gm, "")

        function getWord(num: number, forceWord: boolean = false): Array<number> {
            if (!forceWord && num < 0x100) return [num]

            if (wordLH) return [num & 0xff, (num >> 8) & 0xff]

            return [(num >> 8) & 0xff, num & 0xff]
        }

        StringTokenizer.STRING_PARSER.lastIndex = 0
        let catcher = 10000

        const invalidCodes: any[] = []

        while ((match = StringTokenizer.STRING_PARSER.exec(str))) {
            catcher--

            if (catcher < 0) {
                throw `Too many iterations!`
            }
            // console.log(match)

            const gr = StringTokenizer.getGroups(match)
            // console.log(gr)

            const rxTotal = match[0]

            if (rxTotal === '' || rxTotal == undefined) break

            let val: Array<number> | number = []

            if (gr.deci !== undefined) {
                const rawStr = gr.deci.trim()
                val = getWord(parseInt(rawStr, 10), rawStr.slice(-1).toLowerCase() == 'w')
                // console.log(`${gr.deci} -> ${val}`)
            } else if (gr.hexa !== undefined) {
                const rawStr = gr.hexa.replace(/[$]|(0x)/, '')

                val = getWord(parseInt(rawStr, 16), rawStr.slice(-1).toLowerCase() == 'w')
                // console.log(`${gr.hexa} -> ${val}`)
            } else if (gr.str_extra_fn !== undefined) {
                if (funcs[gr.str_extra_fn] === undefined) {
                    const lowercaseFuncs = Object.keys(funcs).map((x) => x.toLowerCase())
                    const lowercaseCalled = gr.str_extra_fn.toLowerCase()
                    if (lowercaseFuncs.includes(lowercaseCalled)) {
                        const correctFunction =
                            Object.keys(funcs)[lowercaseFuncs.indexOf(lowercaseCalled)]
                        val = funcs[correctFunction](...gr.str_extra_fnparam.split(/,\s*/g))

                        // throw new Error(`Function not implemented: ${gr.str_extra_fn}.\nDid you mean to call ${correctFunction} instead?`)
                    } else {
                        throw new Error(`Function not implemented: ${gr.str_extra_fn}`)
                    }
                } else {
                    val = funcs[gr.str_extra_fn](...gr.str_extra_fnparam.split(/,\s*/g))
                }
            } else if (gr.code !== undefined) {
                if (tokens[gr.code] !== undefined) {
                    val = tokens[gr.code]
                } else {
                    val = tokens['undefined']
                    const lowercaseCode = gr.code.toLowerCase()
                    const lowercaseCodes = Object.keys(tokens).map((x) => x.toLowerCase())
                    const index = lowercaseCodes.indexOf(lowercaseCode)

                    if (index >= 0) {
                        val = Object.values(tokens)[index]
                    }
                }

                if (val === undefined) {
                    invalidCodes.push({ code: gr.code, pos: (props.pos || 0) + match.index })
                    val = [63]
                    // throw new Error(`Undefined code: ${gr.code}!`)
                }
            } else if (gr.sep && gr.sep.trim() == ',') {
                // console.log(`Sep: ${gr.sep}`)
                val = [0]
            }
            let multi = 1

            if (gr.multi) {
                multi = parseInt(gr.multi, 10)
                // console.log(`Multi: ${gr.multi} -> ${multi}`)
            }

            if (typeof val === 'number') val = [val]

            for (let idx = 0; idx < multi; idx++) {
                byteArray.push(...val)
            }

            // console.log(`Adding [${val}] ${multi} times, collected: ${byteArray}`)
        }

        const status = invalidCodes.length > 0 ? 'ERROR' : 'OK'
        return { byteArray, status, invalidCodes }
    }

    /** Converts text to bytes - special text must be {} bracketed.
     *   Returns:
     *      { result, statusFinal, statusDetails }
     *   Props:
     *    * tokens: <token name - code number> pairs
     *    * funcs:  <function name - function> pairs
     *    * worldLH: LO.HI or HI.LO order
     *    * useUppercase: use uppercase or not for codes - default: not. Use this with uppercase token dict to make case insensitive
     *    * normalConverter: function to convert characters, default: String.charCodeAt
     */
    static convertStringToBytes(text: string, props: any): Record<string, any> {
        const result: number[] = []
        const statusDetails: any[] = []

        let match, value
        let statusFinal: string = 'OK'

        const convertNormal =
            props.normalConverter || ((str: string) => String.prototype.charCodeAt.bind(str)(0))

        const re = /({[^}]+})|(.)/g

        while ((match = re.exec(text))) {
            const [_full, bracket, normal] = match

            if (bracket) {
                props.pos = match.index
                const { byteArray, status, invalidCodes } = StringTokenizer.convertSpecToBytes(
                    bracket.slice(1, -1),
                    props,
                )

                if (status !== 'OK') {
                    statusFinal = status
                    statusDetails.push(...invalidCodes)
                }
                result.push(...byteArray)
            } else {
                value = convertNormal(normal)
                result.push(value)
            }
        }
        return { byteArray: result, status: statusFinal, invalidCodes: statusDetails }
    }
}
