import { COMMANDS_TTC, FUNCTIONS_BY_NAME, TOK_PROP } from '@zoli8000/gb-consts'

import { Token } from '../token'
import { TokenBuilderItem, Tokenizer, TokenizerDefinition } from '../tokenizer'
import { TokenizerSettings } from '../tokenizer-settings'

import { GB } from './gb-consts'
import { GBDecoder } from './gb-decoder'
import { GBPrettifier } from './gb-prettifier'

const orderByLen = (a: string, b: string) => b.length - a.length
const commandRx = Object.keys(COMMANDS_TTC).sort(orderByLen).join('|')
const functionsRx = Object.entries(FUNCTIONS_BY_NAME)
    .map(([name, props]) => {
        const [_code, fnProps] = props
        return fnProps & TOK_PROP.FN_WITH_BRACKET ? String.raw`${name}\(` : name
    })
    .sort(orderByLen)
    .map((v) => (v.slice(-1) == '(' ? v : v + '(?![a-zA-Z_])'))
    .join('|')

export const enum VerboseMode {
    /** Autodetect mode is used. */
    AUTO = 0,
    /** Verbose mode is used. */
    VERBOSE = 1,
    /** Short mode is used. */
    SHORT = 2,
}

export class GBTokenizerSettings extends TokenizerSettings {
    /** Is verbose, short or autodetect to be used? */
    verboseMode = VerboseMode.AUTO
}

export class GBTokenizerSettingsShort extends GBTokenizerSettings {
    verboseMode: VerboseMode = VerboseMode.SHORT
}

export class GBTokenizerSettingsVerbose extends GBTokenizerSettings {
    verboseMode: VerboseMode = VerboseMode.VERBOSE
}

export class GBTokenizerSettingsAuto extends GBTokenizerSettings {
    verboseMode: VerboseMode = VerboseMode.AUTO
}

export class GBTokenizer extends Tokenizer {
    constructor(gbSettings: Partial<GBTokenizerSettings> = <GBTokenizerSettings>{}) {
        const verboseSettings: Record<VerboseMode, any> = {
            [VerboseMode.VERBOSE]: {
                gbIdentifier: 'gbIdentifierLong',
                gbDim: 'gbDimLong',
            },
            [VerboseMode.SHORT]: {
                gbIdentifier: 'gbIdentifierShort',
                gbDim: 'gbDimShort',
            },
            [VerboseMode.AUTO]: {},
        }

        let verboseMode: VerboseMode = gbSettings.verboseMode || VerboseMode.AUTO
        if (verboseMode === VerboseMode.AUTO) {
            verboseMode = VerboseMode.VERBOSE
        }

        const special: TokenizerDefinition = {
            gbIdentifierLong: [8000, String.raw`(?<identifier>[\w×]+)`],
            gbIdentifierShort: [8000, String.raw`(?<identifier>[\w][×]?)`],

            gbSysVar: [8100, String.raw`(?<sysId>[@][\w×]+)`],
            gbPostfix: [8200, String.raw`(?<postfix>\.[\w×]+)`],
            gbLabel: [4000, String.raw`(?<label>^[\w]+\s*[:]$)`],

            gbDimLong: [
                6000,
                String.raw`(?<command__dim>dim)(?<dimDetailsLong>[^;]*)(?<whiteSpc__dim>;)`,
            ],
            gbDimShort: [
                6000,
                String.raw`(?<command__dim>dim)(?<dimDetailsShort>[^;]*)(?<whiteSpc__dim>;)`,
            ],

            // gbCommand: [6050, String.raw`(?<command>\?|local|break|call|def|endif|for|goto|fend|if|elif|clr|let|memset|next|on|poke|continue|return|sprite|then|until|voice|while|exec|input|open|data|bitmap|fcall|dend|else|fast|gosub|close|dim|get|color|loop|memcopy|end|do|dpoke|font|read|sys|timer|wait|inkey|print|locate|inc|dec|repeat)`],
            gbCommand: [6050, String.raw`(?<command>\?|${commandRx})(?![a-zA-Z_])`],
            // gbFunction: [6100, String.raw`(?<func>abs\(|between\(|chr\(|dpeek\(|left\(|fn|sgn\(|has\(|int\(|tab\(|cos\(|len\(|mid\(|atn\(|ord\(|peek\(|frac\(|rnd\(|sin\(|tan\(|sub\(|val\(|when\(|find\(|time\(|spc\(|addr\(|right\(|limit\(|to|step)`],
            gbFunction: [6100, String.raw`(?<func>${functionsRx})`],
            gbOperator: [
                6200,
                String.raw`(?<operator>(AND|OR|div|mod|xor|and|or)(?![a-zA-Z_])|[+\-*\/^>=<&])`,
            ],
            v2Sep: [7000, String.raw`(?<commandSep>[:])`],
            newLines: [3050, String.raw`(?<newLines>(\r\n|\n|\r)+)`],
            whiteSpace: [3100, String.raw`(?<whiteSpc>[\t\ \xA0]+)`],
            gbTypes: [
                6300,
                String.raw`(?<idType>([us]?(byte|word|f08|f88|f2416|float))|char|dchar|varstr|fixstr|[$])`,
            ],
            gbRemDash: [1150, String.raw`(?<remarkDash>[\-]{2}.*$)`],
            gbSysBlock: [1000, String.raw`(?<sysBlock>[#]start.*?[#]end\s*)`],
            gbSysCmd: [1200, String.raw`(?<sysCmd__const>[#]const[^;]+?[;])`],
            gbSysUse: [1200, String.raw`(?<use>^\s*[#]use\s+[\w=,]+)`],
            gbSysCmdOther: [1250, String.raw`(?<sysCmd__other>[#].*$)`],
            gbExpr: [6300, String.raw`(?<expr>[{][^}]*?[}])`],
            gbOther: [8500, String.raw`(?<other__8500>[@,;\(\)\[\]])`],

            gbStrQQuote: [
                2050,
                String.raw`(?<other_quote__strQO>\"\")(?<strQQuote>(?:[{][^}]*?[}]|.)*?)(?<other_quote__strQC>\"\")`,
            ],
            gbStrDQuote: [
                2100,
                String.raw`(?<other_quote__strDO>\")(?<strDQuote>(?:[{][^}]*?[}]|[^\"])*?)(?<other_quote__strDC>\")`,
            ],
            gbStrSQuote: [
                2200,
                String.raw`(?<other_quote__strSO>\')(?<strSQuote>(?:[{][^}]*?[}]|[^\'])*?)(?<other_quote__strSC>\')`,
            ],
            gbStrBQuote: [
                2300,
                String.raw`(?<other_quote__strBO>\`)(?<strBQuote>(?:[{][^}]*?[}]|[^\`])*?)(?<other_quote__strBC>\`)`,
            ],
        }

        const rx = Tokenizer.buildRxString(
            [
                TokenBuilderItem.remark,
                TokenBuilderItem.docstr,
                TokenBuilderItem.remMulti,
                TokenBuilderItem.numInt,
                TokenBuilderItem.numFloat,
                'gbSysCmdOther',
                'gbSysCmd',
                'gbSysBlock',
                'gbSysUse',
                'gbStrQQuote',
                'gbStrDQuote',
                'gbStrSQuote',
                'gbStrBQuote',
                'gbExpr',
                'gbOther',
                'gbRemDash',
                'gbSysVar',
                verboseSettings[verboseMode].gbIdentifier,
                'gbPostfix',
                'gbLabel',
                'gbOperator',
                'gbTypes',
                verboseSettings[verboseMode].gbDim,
                'gbCommand',
                'gbFunction',
                'newLines',
                'v2Sep',
                'whiteSpace',
            ],
            special,
            '',
        )

        /** These pattern groups will be further tokenized to sub-groups. */
        const subFormatPatterns = {
            // TODO: other quote types as well!!!!
            strDQuote: String.raw`  (
                                      (?<other__sp_open>[{])
                                      (?<strSpecial>[^}]*?)
                                      (?<other__sp_close>[}])
                                    ) |
                                    (?<strNormal>[^{]+)`,
            strSpecial: String.raw`
            (?<whiteSpc__100>\s*?)(
             (?<remark__str>(([-]{2}|[/]{2}).*$)|[/][*][\s\S]*?[*][/]) |

             ( (?<func__str>\w+\s*)
                (?<other__bropen>[(])
                (?<strfunc_params>[^)]*?)
               (?<other__brclose>[)])
             ) |

             (
               (?<strSpecCheck>\w+) |
               (?<intNum__1>[a-zA-Z_][a-zA-Z_0-9]*) |
               (?<intNum__2>(?:[$]|0x)[0-9a-fA-F]+[w]{0,1}) |
               (?<intNum__3>[0-9]+[w]{0,1})
             )
             ((?<other__multi>\s*[*]\s*)(?<intNum__str>[0-9]+))?
             (?<other__100>[,;]\s*|\s+|\s*$)
           ) |
           (?<other__101>\s+) |
           (?<error>.+?)
           `,
            strSpecCheck: GBTokenizer.validateCode,

            strfunc_params: String.raw`
                            (?<other__spec100>\")(?<strNormal__strspec>[^\"]*?)(?<other__spec150>\") |
                            (?<intNum__spec100>\d+) |
                            (?<other__spec200>[,]) |
                            (?<other__spec300>.)
                            `,

            dimDetailsShort: '__main',
        }
        // 'const': /(\s*)(?:\#const)|\W(?<type>UBYTE|SBYTE|BYTE|UWORD|SWORD|WORD|UF08|SF08|F08|UF88|SF88|F88|U2416|S2416|F2416|FLOAT|CHAR|DCHAR|VARSTR|FIXSTR|[wlbdW%$BSscLfF]|([$]\d+,?)|[$])\W|(?<const_name>[A-Z_]+)\s*=\s*([{](?<const_expr>.+?)[}]|(?<const_val>.+?))\s*,?(?<comment>[-|\/]{2}.*)?$\s*$/gim,

        // 'dim': /(\s*)(?<cmd>dim|local)|\W(?<type>UBYTE|SBYTE|BYTE|UWORD|SWORD|WORD|UF08|SF08|F08|UF88|SF88|F88|U2416|S2416|F2416|FLOAT|CHAR|DCHAR|VARSTR|FIXSTR|[wlbdW%$BSscLfF]|([$]\d+)|[$])(?<arr_size>\d*)($|\s+)|((?<var_name>[IJK]×|[a-zA-Z][a-zA-Z_0-9×]*)\s*([,;]?\s*(?<comment>[-|\/]{2}.*)?$))/gim,

        const mapper = {
            // 'SYS': 'color:#d8e;font-weight:600',
            // 'ERR': 'color:red; text-decoration-line: underline; text-decoration-style: wavy; text-decoration-color: red',
            // 'SPEC': 'color:#ba8;font-style:italic;font-weight:500',

            command: 'CMD',
            commandRem: 'CMD',
            func: 'FUNC',
            error: 'ERR',

            strDQuote: 'STR',
            strQQuote: 'STR',
            strSQuote: 'STR',
            strBQuote: 'STR',
            strNormal: 'STR',
            strSpec: 'SPEC',

            label: 'LABEL',
            floatNum: 'NUM',
            intNum: 'NUM',

            identifier: 'VAR',
            identifier2: 'VAR',
            idExtra: 'EXTRA',
            idType: 'FUNC',
            sysId: 'FUNC',

            commandSep: 'SEP',
            whiteSpc: 'SPC',
            remark: 'REM',
            remarkDash: 'REM',
            rem_multi: 'REM',
            docstr: 'DOCSTR',
            other: 'OTHER',
            other_quote: 'SKIP',

            sysCmd: 'SYS',
            sysCmd2: 'SYS',
            postfix: 'FUNC',
        }

        const settings = new TokenizerSettings({
            rxPattern: rx,
            subFormatPatterns,
            mapper,
            htmlClassPrefix: 'editor-style-',
            // splitFunc: Tokenizer.lineSplitter,
            PrettifierClass: GBPrettifier,
        })

        super(settings)
    }

    static validateCode(key: string, value: string) {
        if (value.toUpperCase() in GB.TTC_STRING_TOKENS_UPPER) return ['strSpec', value]
        return ['error', value]
    }

    getTokensFromMemory(memory: number[]) {
        const rawTokens = GBDecoder.getTokensFromMemory(memory)
        this.reset()

        rawTokens.forEach((token: Token) => {
            this.addToken(token.key, String(token.value), token.pos)
        })

        // console.log(this.tokenizer.tokens)
    }

    tokenizeStr(text: string) {
        this.tokenizeString(text)
    }
}
