import { LanguageEncoder } from '../language-encoder'
import { StringTokenizer } from '../string-tokenizer'
import { Token } from '../token'
import { TokenArray } from '../token-array'

import { C64Chr } from './c64-chr'
import { GB } from './gb-consts'
import { DimTokenizer } from './gb-dim-encoder'
import { NumberTokenizer, defaultNumberEncoder, GBTypeCodes } from './gb-number-encoder'
import { GbDirectives, GBLanguagePreprocessor } from './gb-preprocessor'
import { GBTokenizer, GBTokenizerSettings } from './gb-tokenizer'
import { VarTokenizer, defaultVarEncoder } from './gb-var-encoder'

type EncoderState = {
    isVerbose: boolean
    varTokenizer: VarTokenizer
    lastCommand: string
}

export const DEFAULT_STRING_TOKENIZER_PROPS = {
    tokens: GB.TTC_STRING_TOKENS,
    normalConverter: C64Chr.textToScreen,
    funcs: GB.gbFuncs,
}

export class GBEncoder extends LanguageEncoder {
    varTokenizer: VarTokenizer
    // readonly RX_LEXER: RegExp = /(?<CMD>end|for|next|data|input#|input|dim|read|let|goto|run|if|restore|gosub|return|rem|stop|on|wait|load|save|verify|def|poke|print#|print|cont|list|clr|cmd|sys|open|close|get|new)|(?<FUNC>tab\(|to|fn|spc\(|then|not|step|\+|-|\*|\/|\^|and|or|>|=|<|sgn|int|abs|usr|fre|pos|sqr|rnd|log|exp|cos|sin|tan|atn|peek|len|str\$|val|asc|chr\$|left\$|right\$|mid\$)|(?<STR>\"[^\"]*\")|(?<NUM>(\d+[.]?\d*)|(\d+)|([.]\d*))|(?<VAR>[a-z][a-z0-9]*[$%(]?)|(?<OTHER>.)/g

    constructor(_gbSettings: Partial<GBTokenizerSettings> = <GBTokenizerSettings>{}) {
        super()
        this.varTokenizer = new VarTokenizer(GBTypeCodes, defaultVarEncoder)
    }

    static encoders: Record<string, any> = {
        idType: GBEncoder.stringEncoder,
        postfix: GBEncoder.varEncoder,
        STR: GBEncoder.stringEncoder,
        NUM: GBEncoder.numEncoder,
        VAR: GBEncoder.varEncoder,
        SEP: GBEncoder.stringEncoder,
        CMD: GBEncoder.cmdEncoder,
        FUNC: GBEncoder.cmdEncoder,
        operator: GBEncoder.cmdEncoder,
        OTHER: GBEncoder.otherEncoder,
        commandSep: LanguageEncoder.voidEncoder,
        SKIP: LanguageEncoder.voidEncoder,
        whiteSpc: LanguageEncoder.voidEncoder,
        sysCmd: LanguageEncoder.voidEncoder,
        newLines: LanguageEncoder.voidEncoder,
        dimDetailsLong: GBEncoder.dimEncoder,
    }

    static numEncoder(token: Token, _state: EncoderState): number[] {
        if (token.key == 'intNum') {
            const nt = new NumberTokenizer(GBTypeCodes, defaultNumberEncoder)
            return nt.integerToArray(token.value)
        }

        throw TypeError(`Invalid type for ${token.key}: ${token.value}`)
    }

    static dimEncoder(token: Token, _state: EncoderState): number[] {
        const dimTokenizer = new DimTokenizer()
        const dimText = token.value
        // eslint-disable-next-line unused-imports/no-unused-vars
        const { vars, errors } = dimTokenizer.parseDimLong(dimText, true)
        const mappedVars = dimTokenizer.assignVariableNames(vars)
        const dimBytes = dimTokenizer.encodeDimLong(mappedVars)
        return dimBytes
    }

    static stringEncoder(token: Token, _state: EncoderState): number[] {
        if (token.key == 'other' && token.value == '?') return [GB.TTC_COMMAND_TOKENS['print']]

        const prefix: number[] = []

        const { byteArray, status, _invalidCodes } = StringTokenizer.convertStringToBytes(
            token.repr,
            DEFAULT_STRING_TOKENIZER_PROPS,
        )

        if (status !== 'OK') console.log(status)

        if (['strDQuote', 'strSQuote', 'strBQuote', 'strQQuote'].includes(token.key)) {
            const quoteToken = GB[token.key as keyof typeof GB] as number
            prefix.push(quoteToken)
            prefix.push(byteArray.length)
        }

        return prefix.concat(byteArray)
    }

    static otherEncoder(token: Token, state: EncoderState): number[] {
        if (state.lastCommand == 'clr' && token.repr == ',') {
            return []
        }

        return GBEncoder.stringEncoder(token, state)
    }

    static varEncoder(token: Token, state: EncoderState) {
        const result = state.varTokenizer.subTokenize(token.repr, state.isVerbose)
        return result
    }

    static cmdEncoder(token: Token, state: EncoderState): number[] {
        const token_repr = token.repr.replace('(', '')
        const convertedToken = GB.TTC_COMMAND_TOKENS[token_repr]
        const extraZeros = GB.EXTRA_ZEROS[token_repr]
        if (extraZeros > 0) {
            return [convertedToken, ...Array(extraZeros).fill(0)]
        }

        if (GB.IS_COMMAND[token_repr]) {
            state.lastCommand = token_repr
        }

        return [convertedToken]
    }

    static encodeTextAsBytes(
        text: string,
        gbSettings: Partial<GBTokenizerSettings> = <GBTokenizerSettings>{},
    ): number[] {
        const preprocessor = new GBLanguagePreprocessor(text)
        preprocessor.setSettings(gbSettings)
        preprocessor.useGlobalDirectives()

        const tokenizer = new GBTokenizer(gbSettings)
        tokenizer.tokenizeString(text)
        const _varsUsed = preprocessor.analyzeVarsFromTokenized(tokenizer.tokens)
        // const dimDetails = preprocessor.getDimDetails()

        const gbDirectives = preprocessor.getFinalDirectives()

        return GBEncoder.encodeTokens(tokenizer.tokens, gbDirectives)
    }

    static encodeTokens(array: TokenArray, gbDirectives: GbDirectives): number[] {
        const lines: any[] = []

        const lineData = { nextAddr: 0, line: null as unknown as number, data: [] as number[] }

        function closeLast() {
            if (lineData.data.length > 0) {
                const lineDataCopy = Object.assign({}, lineData)
                lines.push(lineDataCopy)

                lineData.data = []
            }
        }

        const state: EncoderState = {
            isVerbose: gbDirectives.verbose,
            varTokenizer: new VarTokenizer(GBTypeCodes, defaultVarEncoder),
            lastCommand: '',
        }

        array.forEach((token: Token) => {
            if (token.category == 'LABEL') {
                closeLast()

                const _lineVal = parseInt(token.value, 10)
                return
            }

            const encoderFunction =
                GBEncoder.encoders[token.key] || GBEncoder.encoders[token.category]
            if (encoderFunction) {
                const data = encoderFunction(token, state)
                lineData.data.push(...data)
            } else {
                throw Error(
                    `Encoder needed for ${token.key}, ${token.category} for value ${token.value}!`,
                )
            }
        })
        closeLast()

        const finalBytes: number[] = []

        lines.forEach((line) => {
            finalBytes.push(...line.data, GB.blockEndToken)
        })

        finalBytes.push(GB.finalToken)

        return finalBytes
    }

    /*
    deployCode(data: number[], state) {
        const start = 0x801
        const memData = { start, data }

        app.emulator.setDataInRam(memData)
        app.emulator.onLoaded(memData);

        const memEnd = start + data.length + 5;

        [0x2e, 0x30, 0x32].forEach(changeAddr => {
            state.globalState.emulator.bus.write(changeAddr, (memEndvarTokenizer >> 8))
            state.globalState.emulator.bus.write(changeAddr - 1, memEnd & 0xff)
        })
    }
    */
}
