import { LanguageModel, LANGUAGE} from "./src/";
import { GBTokenizerSettings } from "./src/languages/gb-tokenizer";

declare const require: any;
declare const monaco: any;

require.config({ paths: { vs: "https://cdn.jsdelivr.net/npm/monaco-editor@0.44.0/min/vs" } });

// @ts-ignore
import { createJSONEditor } from "https://cdn.jsdelivr.net/npm/vanilla-jsoneditor@latest/+esm"
import { GBLanguagePreprocessor } from "./src/languages/gb-preprocessor";

var jsonEditor = createJSONEditor({
  target: document.getElementById("tokens"),
  props: {
    mode: "table",             // default active mode
    modes: ["tree", "table"], // allowed modes only
    mainMenuBar: true,
    navigationBar: false,
    readOnly: true,
  }
})
jsonEditor.update({ json: { hello: "world" } })

require(["vs/editor/editor.main"], () => {
  const editor = monaco.editor.create(document.getElementById("editor"), {
    value: `10 poke 53280, 2`,
    language: "plaintext", // will change dynamically
    theme: "vs-dark",
    automaticLayout: true
  });

  const tokensContainer = document.getElementById("tokens")!;
  const bytecodeContainer = document.getElementById("bytecode")!;
  const langSelect = document.getElementById("lang") as HTMLSelectElement;

  function updateOutput(): void {
    const code: string = editor.getValue();
      const lang: string = langSelect.value;
    bytecodeContainer.textContent = code // bytecode.join(" ");

    const { Encoder, Decoder, Tokenizer, Preprocessor } = new LanguageModel().usePredefinedLanguage(LANGUAGE.GOLFBAS_verbose).model
    const preprocessor = new Preprocessor(code)
    // preprocessor.setSettings(gbSettings)
    preprocessor.useGlobalDirectives()
    const gbDirectives = preprocessor.getFinalDirectives()

    const gbSettings = <GBTokenizerSettings>{}
    const tokenizer = new Tokenizer(gbSettings)
    const _varsUsed = preprocessor.analyzeVarsFromTokenized(tokenizer.tokens)

    tokenizer.tokenizeString(code);
    const tokens = tokenizer.tokens
    const bytecode: number[] = Encoder.encodeTokens(tokenizer.tokens, gbDirectives)
    const transformedTokens = tokens.map((item) => {
        return {
          key: item.key,
          category: item.category,
          value: item.value,
          pos: item.pos,

        }
    })

    jsonEditor.update({ json: transformedTokens })
  }

  let typingTimer: number | undefined;

  function scheduleUpdate(): void {
    if (typingTimer) clearTimeout(typingTimer);
    typingTimer = window.setTimeout(updateOutput, 1000); // 1s
  }

  editor.onDidChangeModelContent(scheduleUpdate);
  updateOutput();

  langSelect.addEventListener("change", updateOutput);


});
