import { Prettifier, BasePrettifier } from './prettifier'
import { Token } from './token'
import { TokenArray } from './token-array'
import { TokenizerFunction, Tokenizer } from './tokenizer'

export type TokenizerSettingKeys =
    | 'rxPattern'
    | 'skipEmpty'
    | 'TokenClass'
    | 'mapper'
    | 'PrettifierClass'
    | 'htmlClassPrefix'
    | 'splitFunc'
    | 'subFormatPatterns'
    | 'getNewLineCountFunction'
    | 'tokenTypeMapper'

export class TokenizerSettings {
    /** Regexp used for tokenizing. The default tokenizes each character as a symbol. */
    rxPattern: RegExp

    /** Further tokenization for given tokens */
    subFormatPatterns: Record<string, TokenizerFunction>

    /** Skip adding empty token results. Default: true.*/
    skipEmpty: boolean

    /** Mapper: a dictionary where key is the regexp name, and value is the final token type. */
    mapper: Record<string, string>

    /** A Token or an inherited class. Default: Token. */
    TokenClass: typeof Token

    /** A Prettifier class which can prettify tokens and line indentations. */
    PrettifierClass: typeof Prettifier

    /** Prefix for html classes. */
    htmlClassPrefix: string

    /** Split function for tokens - default: none */
    splitFunc: (token: Token, settings: TokenizerSettings, TokenClass: typeof Token) => Token[]

    /** A function returning the new lines to be added from a Token. */
    getNewLineCountFunction: ((token: Token, array: TokenArray, idx: number) => number) | null

    /** Mappings for type checkings, e.g. isString */
    tokenTypeMapper: Record<string, string[]>

    /** Settings for tokenizer
     * @param rxPattern         Regexp used for tokenizing. The default tokenizes each character as a symbol.
     * @param subFormatPatterns        Further tokenization for given tokens.
     * @param skipEmpty         Skip adding empty token results. Default: true.
     * @param TokenClass        A Token or an inherited class. Default: Token.
     * @param mapper            Mapper: a dictionary where key is the regexp name, and value is the final token type.
     * @param PrettifierClass   A Prettifier class which can prettify tokens and line indentations.
     * @param htmlClassPrefix   Prefix for html classes.
     * @param getNewLineCount   A function returning the new lines to be added from a Token.
     * @param prettifierFunction Prettifier function - add whitespace, format commands etc.
     * @param tokenTypeMapper   Mappings for type checkings, e.g. isString
     */
    constructor(settings: Partial<Record<TokenizerSettingKeys, any>>) {
        // Validation
        Object.keys(settings).forEach((key) => {
            if (
                [
                    'rxPattern',
                    'subFormatPatterns',
                    'skipEmpty',
                    'mapper',
                    'PrettifierClass',
                    'TokenClass',
                    'splitFunc',
                    'getNewLineCountFunction',
                    'tokenTypeMapper',
                    'htmlClassPrefix',
                ].indexOf(key) == -1
            ) {
                throw Error(`Invalid key ${key} for tokenizer settings!`)
            }
        })

        this.rxPattern = settings.rxPattern || /(?<symbol>.)/g
        this.subFormatPatterns = settings.subFormatPatterns || {}

        this.skipEmpty = settings.skipEmpty === undefined ? true : settings.skipEmpty
        this.mapper = settings.mapper || {}
        this.htmlClassPrefix = settings.htmlClassPrefix || ''
        this.splitFunc = settings.splitFunc || Tokenizer.noSplitter
        this.tokenTypeMapper = settings.tokenTypeMapper || Token.defaultMapper

        this.TokenClass = settings.TokenClass || Token
        this.PrettifierClass = settings.PrettifierClass || BasePrettifier

        this.getNewLineCountFunction = settings.getNewLineCountFunction || null
    }
}
